<template>
        <div class="d-flex vh-100 position-relative"
             v-for="(page,key) in PAGES"
             :key="key">
            <component :is="page.component"
                       :componentLink="key<=PAGES.length-2?PAGES[key+1].link:'#'" :readMore="page.read_more ?? ''"/>
        </div>
</template>
<script>
import {Pages} from "@/components/Consts/Pages"

export default {
    name: 'App',
    data() {
        return {
            PAGES: Pages
        }
    }
}
</script>