<template>
  <div class="container ">
    <div class="row">
      <div class="col ps-4 pe-1 py-2 text-start">
        <blockquote>
          <p class="c8"><span class="c9">Noc deportacji</span></p>
          <p class="c8"><span class="c1 c10">Piotr Korczy&#324;ski </span></p>
          <p class="c2"><span class="c1">Zima na prze&#322;omie 1939 i 1940 roku okaza&#322;a si&#281; wyj&#261;tkowo ostra. Zaspy i mrozy sparali&#380;owa&#322;y &#380;ycie prawie w ca&#322;ej Europie. Dla walcz&#261;cych wojsk by&#322;a to pauza. Jednak w okupowanej Polsce zima nie przynios&#322;a wytchnienia. Gestapo i NKWD czyni&#322;y swoj&#261; zbrodnicz&#261; powinno&#347;&#263;. Zw&#322;aszcza Sowieci szykowali si&#281; do szeroko zakrojonej akcji na polskich Kresach Wschodnich.</span><span
                  class="c3">&nbsp;</span></p>
          <p class="c2"><span class="c3">Po &bdquo;wyzwoleniu Zachodniej Bia&#322;orusi i Zachodniej Ukrainy&rdquo;, jak propaganda sowiecka nazywa&#322;a agresj&#281; Armii Czerwonej na Kresy Rzeczypospolitej, przyst&#261;piono b&#322;yskawicznie do politycznej unifikacji tych ziem z sowieckim imperium. Tempo by&#322;o rzeczywi&#347;cie imponuj&#261;ce: 22 pa&#378;dziernika 1939 roku odby&#322;y si&#281; &bdquo;wybory&rdquo; do zgromadze&#324; ludowych Zachodniej Bia&#322;orusi i Zachodniej Ukrainy. Wy&#322;onieni w czasie tej wyborczej farsy &bdquo;przedstawiciele ludu&rdquo; natychmiast poprosili o w&#322;&#261;czenie &bdquo;Zachodniej Bia&#322;orusi i Zachodniej Ukrainy do wszechzwi&#261;zkowej rodziny sowieckich republik&rdquo;. W konsekwencji Rada Najwy&#380;sza ZSRS podczas posiedzenia 1 i 2 listopada 1939 roku wyrazi&#322;a zgod&#281; na w&#322;&#261;czenie tych ziem w granice sowieckiego pa&#324;stwa. W&oacute;wczas to Wiaczes&#322;aw Mo&#322;otow wypowiedzia&#322; s&#322;ynne zdanie, &#380;e wystarczy&#322;o kr&oacute;tkie uderzenie armii niemieckiej i sowieckiej na Polsk&#281;, aby nic nie zosta&#322;o &bdquo;z tego pokracznego tworu traktatu wersalskiego&rdquo;.</span>
          </p>
          <p class="c2"><span class="c1">Wielki terror</span><span class="c3">&nbsp;</span></p>
          <p class="c2"><span class="c3">W &#347;lad za decyzj&#261; o przy&#322;&#261;czeniu Kres&oacute;w do Zwi&#261;zku Sowieckiego przysz&#322;a kolejna dyrektywa, kt&oacute;ra jeszcze bardziej mia&#322;a zmieni&#263; w koszmar &#380;ycie ich mieszka&#324;c&oacute;w. 29 listopada Rada Najwy&#380;sza ZSRS nada&#322;a obywatelstwo sowieckie wszystkim osobom, kt&oacute;re 1 i 2 listopada zamieszkiwa&#322;y na anektowanych terenach. 30 grudnia 1939 roku ukaza&#322; si&#281; dekret o wydaniu im paszport&oacute;w, czyli sowieckich dowod&oacute;w osobistych. Ta przymusowa &bdquo;paszportyzacja&rdquo; pos&#322;u&#380;y&#322;a NKWD za doskona&#322;y spos&oacute;b do zebrania lub uzupe&#322;nienia danych o ludno&#347;ci Kres&oacute;w, zw&#322;aszcza polskiej, kt&oacute;ra w&oacute;wczas stanowi&#322;a dla sowieckiej s&#322;u&#380;by bezpiecze&#324;stwa najwi&#281;kszy problem. Ci, kt&oacute;rym przypisano obywatelstwo sowieckie z miejsca podlegali poborowi do Armii Czerwonej. W ten spos&oacute;b do jej batalion&oacute;w roboczych od razu wcielono 150 tys. Polak&oacute;w! Ponadto NKWD w grudniu 1939 roku nasili&#322;o fal&#281; aresztowa&#324;. W&oacute;wczas, g&#322;&oacute;wnie w kresowych miastach, uwi&#281;ziono wielu by&#322;ych pracownik&oacute;w polskiej administracji, s&#261;downictwa, dzia&#322;aczy spo&#322;ecznych i gospodarczych oraz oficer&oacute;w rezerwy. Ludzie ci byli na mocy wyrok&oacute;w jawnych lub tajnych trybuna&#322;&oacute;w wywo&#380;eni do oboz&oacute;w pracy. I to by&#322; dopiero pocz&#261;tek &ndash; rozruch wielkiej machiny terroru, kt&oacute;r&#261; NKWD stworzy&#322;o dla ujarzmienia Kres&oacute;w.</span>
          </p>
          <p class="c2" id="h.gjdgxs"><span class="c3">Enkawudzi&#347;ci doskonale zdawali sobie spraw&#281;, &#380;e &#380;adna propaganda ani indoktrynacja nie zmieni Polak&oacute;w w &bdquo;sowieckich ludzi&rdquo;. Zreszt&#261; mieli tego wyra&#378;ne dowody. Mi&#281;dzy innymi 21 stycznia 1940 roku w Czortkowie (w&oacute;wczas ju&#380; w Ukrai&#324;skiej Socjalistycznej Republice Sowieckiej) grupa polskiej m&#322;odzie&#380;y licealnej, oko&#322;o 120-150 os&oacute;b, wywo&#322;a&#322;a powstanie. Cz&#281;&#347;ciowo uzbrojona m&#322;odzie&#380; opanowa&#322;a dworzec kolejowy, aby w perspektywie przej&#261;&#263; kontrol&#281; nad szlakiem komunikacyjnym do Rumunii, sk&#261;d mo&#380;na by&#322;o przedosta&#263; si&#281; do armii polskiej we Francji. Po pierwszej panice &#380;o&#322;nierzy garnizonu sowieckiego bunt szybko st&#322;umiono. O&#347;miu buntownik&oacute;w stracono w Odessie, a reszt&#281; uwi&#281;ziono, ale to pokazywa&#322;o, &#380;e Mo&#322;otow przedwcze&#347;nie og&#322;osi&#322; definitywny koniec Polski. NKWD si&#281;gn&#281;&#322;a w&oacute;wczas do sprawdzonych metod represjonowania nie tylko pojedynczych ludzi, ale ca&#322;ych &#347;rodowisk i grup spo&#322;ecznych. Zacz&#281;to gromadzi&#263; na masow&#261; skal&#281; sk&#322;ady kolejowe i wyznacza&#263; trasy deportacji w najdalsze rejony przepastnego imperium sowieckiego.</span>
          </p>
          <p class="c2"><span class="c1">Mro&#378;ny koszmar</span><span class="c3">&nbsp;</span></p>
          <p class="c2"><span class="c3">Wraz z kolejn&#261; fal&#261; mroz&oacute;w i &#347;nie&#380;yc NKWD w nocy z 9 na 10 lutego 1940 roku rozpocz&#281;&#322;a sw&#261; akcj&#281; deportacyjn&#261;. Ta straszna noc w tysi&#261;cach polskich miejscowo&#347;ci, osiedli i przysi&oacute;&#322;k&oacute;w mia&#322;a ten sam, szczeg&oacute;&#322;owo zaplanowany przez enkawudzist&oacute;w przebieg. Wed&#322;ug list proskrypcyjnych otaczano dom wyznaczonej do deportacji rodziny. Brutalnie wyrwanym ze snu ludziom pozwalano zabra&#263; rzeczy osobiste, naczynia kuchenne i &#380;ywno&#347;&#263; na kilka tygodni. Nast&#281;pnie przesiedle&#324;c&oacute;w wieziono saniami lub p&#281;dzono pieszo do najbli&#380;szej stacji kolejowej, gdzie na bocznicy czeka&#322;y ju&#380; na nich bydl&#281;ce wagony. Tak t&#281; chwil&#281; zapami&#281;ta&#322;a mieszkanka Lipowca pod Drohobyczem, w&oacute;wczas nastoletnia Adela &#379;urawska: &bdquo;&#379;o&#322;nierze sowieccy, kt&oacute;rzy otoczyli nasz dom, dali nam chwil&#281; na spakowanie najwa&#380;niejszych rzeczy i za&#322;adowali wszystkich do sa&#324;. Zosta&#322;y&#347;my dowiezione do stacji kolejowej w Zborowie. Tam ju&#380; sta&#322; pod par&#261; bardzo d&#322;ugi poci&#261;g z&#322;o&#380;ony z bydl&#281;cych wagon&oacute;w. W naszym &#347;ci&#347;ni&#281;to oko&#322;o pi&#281;&#263;dziesi&#281;ciu os&oacute;b. Ruszyli&#347;my po czterech czy pi&#281;ciu dniach na wsch&oacute;d. [&hellip;] Podni&oacute;s&#322; si&#281; wtedy jeden p&#322;acz &ndash; ludzie rozpaczali i si&#281; modlili&rdquo;. Ta lutowa deportacja dotkn&#281;&#322;a przede wszystkim osadnik&oacute;w wojskowych, le&#347;nik&oacute;w i innych urz&#281;dnik&oacute;w pa&#324;stwowych oraz kolejarzy, oczywi&#347;cie wraz z rodzinami. Je&#347;li ojcom rodzin udowodniono, &#380;e byli oficerami, przedsi&#281;biorcami czy ziemianami, po wyroku s&#261;d&oacute;w dora&#378;nych kierowano ich do &#322;agr&oacute;w. Reszt&#281; deportowanych w lutym 1940 roku &ndash; czyli g&#322;&oacute;wnie kobiety z dzie&#263;mi oraz starc&oacute;w &ndash; wywo&#380;ono na Ural i Syberi&#281;, gdzie w dramatycznie trudnych warunkach musieli &bdquo;rozpoczyna&#263; &#380;ycie na nowo&rdquo;. Wielu z nich nie dane by&#322;o prze&#380;ywanie tego koszmaru, umarli z wych&#322;odzenia i g&#322;odu w czasie transportu.</span>
          </p>
          <p class="c2"><span class="c4">Akcja z 9 na 10 lutego 1940 by&#322;a dopiero pierwsz&#261; z ca&#322;ej serii deportacji, kt&oacute;r&#261; NKWD zaplanowa&#322;o, by raz na zawsze oczy&#347;ci&#263; zdobyczne ziemie z &bdquo;polskiego &#380;ywio&#322;u&rdquo;. W kwietniu ofiarami masowych wyw&oacute;zek pad&#322;y rodziny je&#324;c&oacute;w, skazanych i aresztowanych. W ten spos&oacute;b w Kazachstanie znale&#378;li si&#281; mi&#281;dzy innymi najbli&#380;si </span><span
                  class="c4"><a class="c0"
                                href="https://www.google.com/url?q=http://www.polska-zbrojna.pl/home/articleshow/30094?t%3DTak-narodzilo-sie-klamstwo-katynskie&amp;sa=D&amp;source=editors&amp;ust=1616418980885000&amp;usg=AOvVaw2jj96eX_snjfhVzOGUI8Qr">polskich oficer&oacute;w mordowanych w tym samym czasie w Katyniu</a></span><span
                  class="c3">. W czerwcu deportacj&#261; obj&#281;to uciekinier&oacute;w z centralnej i zachodniej Polski. Wbrew temu, co dzisiaj g&#322;osi prezydent Rosji, W&#322;adimir Putin, w&#347;r&oacute;d wywo&#380;onych w&oacute;wczas obywateli polskich znalaz&#322;o si&#281; tysi&#261;ce &#379;yd&oacute;w, kt&oacute;rzy uciekli przed represjami sojusznika Stalina &ndash; Hitlera. Wielu z nich nie prze&#380;y&#322;o transportu w bydl&#281;cych wagonach lub zmar&#322;o na miejscu zes&#322;ania &ndash; w Archangielsku i Krasnojarsku.</span>
          </p>
          <p class="c2"><span class="c4">Wreszcie ostatnia fala deportacji, przeprowadzona przed samym wybuchem wojny niemiecko-sowieckiej w maju i czerwcu 1941 roku, dotkn&#281;&#322;a uciekinier&oacute;w z obszaru Wile&#324;szczyzny. </span><span
                  class="c4"><a class="c0"
                                href="https://www.google.com/url?q=http://www.polska-zbrojna.pl/home/articleshow/26632?t%3DZ-armii-Berlinga-do-korpusu-Andersa&amp;sa=D&amp;source=editors&amp;ust=1616418980886000&amp;usg=AOvVaw3BrRp_XFeQQsoMjI5wmksj">Wywo&#380;ono ich do Kazachstanu</a></span><span
                  class="c4">&nbsp;i na Syberi&#281; dos&#322;ownie do ostatnich dni pokoju mi&#281;dzy III Rzesz&#261; a Zwi&#261;zkiem Sowieckim. Wiele z poci&#261;g&oacute;w z deportowanymi lud&#378;mi Wehrmacht przej&#261;&#322; po wkroczeniu na Kresy 22 czerwca 1941 roku. Bardzo trudno jest oszacowa&#263;, ilu obywateli polskich wywieziono w&oacute;wczas podczas tych czterech faz deportacji. Najcz&#281;&#347;ciej wymienia si&#281; liczb&#281; 400-500 tysi&#281;cy. Pewne jest jedno &ndash; wszystkich czeka&#322;a gehenna w &bdquo;sowieckim raju&rdquo;. Ci, kt&oacute;rzy mieli szcz&#281;&#347;cie przetrwa&#263;, po &bdquo;amnestii&rdquo; w lipcu 1941 roku trafili do </span><span
                  class="c4"><a class="c0"
                                href="https://www.google.com/url?q=http://www.polska-zbrojna.pl/home/articleshow/20052?t%3D73-lata-temu-utworzono-2-Korpus-Polski-&amp;sa=D&amp;source=editors&amp;ust=1616418980886000&amp;usg=AOvVaw0P_RfrbdIjMFRkSTb5zZGh">armii polskiej genera&#322;a W&#322;adys&#322;awa Andersa</a></span><span
                  class="c4">&nbsp;i czeka&#322; ich exodus na Zach&oacute;d. A ci, kt&oacute;rzy &bdquo;nie zd&#261;&#380;yli do Andersa&rdquo; wr&oacute;cili do Polski </span><span
                  class="c4"><a class="c0"
                                href="https://www.google.com/url?q=http://www.polska-zbrojna.pl/home/articleshow/20739?t%3D12-pazdziernika-1943-roku-rozpoczela-sie-bitwa-pod-Lenino&amp;sa=D&amp;source=editors&amp;ust=1616418980886000&amp;usg=AOvVaw3KCLCbRj_l3IL6OBQgOkAD">w szeregach wojska genera&#322;a Zygmunta Berlinga</a></span><span
                  class="c3">. Taki los spotka&#322; cytowan&#261; wy&#380;ej Adel&#281; &#379;urawsk&#261;.</span></p>
          <p class="c2"><span class="c1">Bibliografia</span><span class="c3">&nbsp;</span></p>
          <p class="c2"><span class="c4 c5">Czes&#322;aw Brzoza, Polska w czasach niepodleg&#322;o&#347;ci i drugiej wojny &#347;wiatowej (1918-1945), Krak&oacute;w 2001</span><span
                  class="c4">&nbsp;<br></span><span
                  class="c4 c5">Andrzej Chwalba, Dzieje Polski. Kalendarium, Krak&oacute;w 1999</span><span class="c4">&nbsp;<br></span><span
                  class="c4 c5">Jedno z moich imion brzmi &#380;ycie, praca zbiorowa, Warszawa 2019</span><span
                  class="c3">&nbsp;</span></p>
        </blockquote>
      </div>
