import {createWebHistory, createRouter} from "vue-router";
import SecondWarDescription from "@/components/Pages/SecondWar/SecondWarDescription";
import Home from "@/components/Pages/Home";
import SeptemberSeventeenth from "@/components/Pages/17_09_1939/17_09_Description";
import Soviet from "@/components/Pages/Soviet/SovietDesc";
import SikorskiDesc from "@/components/Pages/Sikorski/SikorskiDesc";
import ZsrsDesc from "@/components/Pages/Zsrs/ZsrsDesc";
import CreateHistoryDesc from "@/components/Pages/CreateHistory/CreateHistoryDesc";
import GeneralAndersDesc from "@/components/Pages/GenAnders/GeneralAndersDesc";
import StartWarDesc from "@/components/Pages/StartWar/StartWarDesc";
import PolandBannerDesc from "@/components/Pages/PolandBanner/PolandBannerDesc";

const routes = [
    {
        path: "/",
        name: "Home",
        component: Home,
    },
    {
        path: "/second-war/description",
        name: "SecondWarDescription",
        component: SecondWarDescription,
    },
    {
        path: "/17-09-1939/description",
        name: "17_09_1939Description",
        component: SeptemberSeventeenth,
    },
    {
        path: "/soviet/description",
        name: "SovietDescription",
        component: Soviet,
    },
    {
        path: "/sikorski/description",
        name: "SikorskiDescription",
        component: SikorskiDesc,
    },
    {
        path: "/zsrs/description",
        name: "ZsrsDescription",
        component: ZsrsDesc,
    },
    {
        path: "/create-history/description",
        name: "CreateHistoryDescription",
        component: CreateHistoryDesc,
    },
    {
        path: "/general-anders/description",
        name: "GeneralAndersDescription",
        component: GeneralAndersDesc,
    },
    {
        path: "/start-war/description",
        name: "StartWarDescription",
        component: StartWarDesc,
    },
    {
        path: "/poland-banner/description",
        name: "PolandBannerDescription",
        component: PolandBannerDesc,
    }
];

const router = createRouter({
    history: createWebHistory(),
    routes: routes,
    scrollBehavior(to) {
        console.log(to.hash);
        if (to.path === '/' && to.hash) {
            return{
                el: to.hash,
                behavior: 'smooth',
            };
        }
        document.getElementById('app').scrollIntoView();
    },
});

export default router;