  <div class="container ">
    <div class="row">
      <div class="col ps-4 pe-1 py-2 text-start">
        <blockquote>
          <p class="c7"><span class="c5 c10">Pakt Sikorski &ndash; Majski: sukces czy zaprzepaszczona szansa?</span></p>
          <p class="c7 c11"><span class="c5 c4"></span></p>
          <p class="c7"><span class="c4 c5">Piotr Korczy&#324;ski</span></p>
          <p class="c1"><span class="c4">Pu&#322;kownik Antoni &#321;api&#324;ski, zes&#322;aniec i &#380;o&#322;nierz 2 Korpusu Polskiego dobrze zapami&#281;ta&#322; dzie&#324;, w kt&oacute;rym dowiedzia&#322; si&#281; o podpisaniu 30 lipca 1941 roku polsko-sowieckiego paktu: &bdquo;Dla nas to by&#322; cud, to by&#322;o jak zmartwychwstanie, ale przede wszystkim by&#322;a to dodatkowa kromka chleba. Niewyobra&#380;alny skarb dla cierpi&#261;cych g&#322;&oacute;d&rdquo;.</span><span
                  class="c2">&nbsp;</span></p>
          <p class="c1"><span class="c3"><a class="c8"
                                            href="https://www.google.com/url?q=http://www.polska-zbrojna.pl/home/articleshow/28303?t%3DNie-badz-glupi-nie-daj-sie-zabic&amp;sa=D&amp;source=editors&amp;ust=1619026199499000&amp;usg=AOvVaw3qW6VFosF3C9zhJK_vwwAY">Antoni &#321;api&#324;ski</a></span><span
                  class="c3">&nbsp;wspomina tak&#380;e przemow&#281; kierownika budowy, na kt&oacute;rej od kilku miesi&#281;cy pracowa&#322; niewolniczo wraz z innymi Polakami, jak on wywiezionymi w g&#322;&#261;b Zwi&#261;zku Sowieckiego w 1940 roku: &bdquo;Bracia Polacy, jeste&#347;cie naszymi przyjaci&oacute;&#322;mi. Wsp&oacute;lnie mamy walczy&#263; przeciwko Niemcom. Od tego dnia jeste&#347;cie lud&#378;mi wolnymi, jeste&#347;cie traktowani, jak nasi sprzymierze&#324;cy&rdquo;. Wczorajsi wrogowie, pozbawieni wszelkich praw zes&#322;a&#324;cy, zmienili si&#281; nagle w sojusznik&oacute;w, kt&oacute;rzy otrzymali szans&#281; walki z broni&#261; w r&#281;ku i powrotu do kraju. To o nich genera&#322; </span><span
                  class="c3"><a class="c8"
                                href="https://www.google.com/url?q=http://polska-zbrojna.pl/home/articleshow/25867?t%3DJak-zginal-general-Sikorski-&amp;sa=D&amp;source=editors&amp;ust=1619026199499000&amp;usg=AOvVaw36bDCGj_bEt0_BpE3Ns469">W&#322;adys&#322;aw Sikorski</a></span><span
                  class="c3">&nbsp;m&oacute;wi&#322; do swego przyjaciela, genera&#322;a Mariana Kukiela: &bdquo;Gdy mia&#322;em ten uk&#322;ad podpisa&#263; i walczy&#322;em ze sob&#261;, czy odczeka&#263; jeszcze, s&#322;ysza&#322;em jakby szept tysi&#281;cy ust: spiesz si&#281;, ratuj!&rdquo;. To mia&#322;a by&#263; te&#380; odpowied&#378; dla tych, kt&oacute;rzy krytykowali Naczelnego Wodza i premiera za zbyt pochopne </span><span
                  class="c3"><a class="c8"
                                href="https://www.google.com/url?q=http://polska-zbrojna.pl/home/articleshow/20105?t%3DMija-75-lat-od-zawarcia-ukladu-Sikorski-Majski&amp;sa=D&amp;source=editors&amp;ust=1619026199500000&amp;usg=AOvVaw36VW-wXFyq4e-WGlBuI0c_">podpisanie paktu z Sowietami</a></span><span
                  class="c2">.</span></p>
          <p class="c1"><span class="c4">Wyczekiwana wojna</span><span class="c2">&nbsp;</span></p>
          <p class="c1"><span class="c2">Na wie&#347;&#263; o niemieckiej agresji na Zwi&#261;zek Sowiecki 22 czerwca 1941 roku premier Wielkiej Brytanii Winston Churchill odetchn&#261;&#322; z ulg&#261;. Hitler, daj&#261;c rozkaz do ataku na swego sojusznika, pope&#322;ni&#322; od dawna wyczekiwany na Zachodzie b&#322;&#261;d &ndash; rozpocz&#261;&#322; wojn&#281; na dwa fronty, a jego przeciwnikiem sta&#322; si&#281; najwi&#281;kszy kraj na &#347;wiecie o olbrzymim potencjale ludzkim. Jeszcze tego samego dnia wieczorem Churchill o&#347;wiadczy&#322;, &#380;e &bdquo;pa&#324;stwo podejmuj&#261;ce z determinacj&#261; walk&#281; przeciwko III Rzeszy, wsp&oacute;lnemu wrogowi wszystkich narod&oacute;w, staje si&#281; automatycznie, bez zastrze&#380;e&#324;, brytyjskim sojusznikiem&rdquo;. Dla Polski w tym zdaniu najwa&#380;niejsze i najgro&#378;niejsze zarazem by&#322;o stwierdzenie &bdquo;bez zastrze&#380;e&#324;&rdquo;. Niew&#261;tpliwie jednak rozbicie sojuszu niemiecko-sowieckiego by&#322;o dla Polak&oacute;w wydarzeniem pozytywnym, nios&#261;cym nadziej&#281;, &#380;e &#347;miertelni wrogowie wykrwawi&#261; si&#281; na tyle, &#380;e znowu b&#281;dzie mo&#380;liwe odzyskanie niepodleg&#322;o&#347;ci. Trzeba te&#380; pami&#281;ta&#263; o tym, &#380;e powszechnie s&#261;dzono wtedy, i&#380; Armia Czerwona nie sprosta Wehrmachtowi i rozsypie si&#281; ca&#322;kowicie pod jego uderzeniem. Wiele na to wskazywa&#322;o po pierwszych bitwach granicznych. Ostateczne zwyci&#281;stwo nad Niemcami, podobnie jak w I wojnie &#347;wiatowej, mia&#322;o przynie&#347;&#263; rozstrzygaj&#261;ce uderzenie aliant&oacute;w na Zachodzie. Najwa&#380;niejsze by&#322;o, &#380;e ogromne armie niemieckie zwi&#261;za&#322;y si&#281; walk&#261; na Wschodzie, wi&#281;c Rosji nale&#380;a&#322;o udzieli&#263; wszelkiej pomocy i poparcia. 12 lipca 1941 roku w Moskwie zosta&#322; podpisany przez Wiaczes&#322;awa Mo&#322;otowa i ambasadora brytyjskiego w ZSRS Stafforda Crippsa uk&#322;ad sowiecko-brytyjski, a 18 lipca zako&#324;czono rokowania sowiecko-czechos&#322;owackie. Tak&#380;e zza oceanu pobieg&#322;y w stron&#281; Kremla s&#322;owa poparcia. Ju&#380; 23 czerwca 1941 roku, w odpowiedzi na deklaracj&#281; Churchilla, ameryka&#324;ski podsekretarz stanu Sumner Welles zaoferowa&#322; Stalinowi poparcie Stan&oacute;w Zjednoczonych.</span>
          </p>
          <p class="c1"><span class="c4">Pakt z diab&#322;em</span><span class="c2">&nbsp;</span></p>
          <p class="c1"><span class="c2">Tego samego dnia, w kt&oacute;rym Welles zadeklarowa&#322; pomoc swego kraju dla Zwi&#261;zku Sowieckiego, w radiu przem&oacute;wienie wyg&#322;osi&#322; genera&#322; W&#322;adys&#322;aw Sikorski. Szef rz&#261;du RP na uchod&#378;ctwie zasugerowa&#322; mo&#380;liwo&#347;&#263; poprawy stosunk&oacute;w polsko-sowieckich. Zasugerowa&#322; przy tym, &#380;e Polska wst&#281;pnie widzi potrzeb&#281; zmiany stanowiska Kremla w kwestii granic, czyli powrotu do granicy ustalonej w traktacie ryskim i uwolnienia z sowieckich wi&#281;zie&#324; i oboz&oacute;w przetrzymywanych tam obywateli polskich. To otworzy&#322;o drog&#281; do roboczych rozm&oacute;w mi&#281;dzy niedawnymi wrogami, kt&oacute;rych g&#322;&oacute;wnym po&#347;rednikiem zosta&#322; minister spraw zagranicznych Wielkiej Brytanii, Anthony Eden. Polsko-sowieckie negocjacje zainicjowano 5 lipca 1941 roku w gmachu Foreign Office. Ze strony polskiej bra&#322; w nich udzia&#322; genera&#322; W&#322;adys&#322;aw Sikorski i minister spraw zagranicznych RP, August Zaleski. Rz&#261;d sowiecki reprezentowa&#322; ambasador ZSRS w Londynie, Iwan Majski. Rozmowy protoko&#322;owa&#322; Aleksander Cadogan, podsekretarz stanu Foreign Office. Po kolejnym spotkaniu obu stron, do kt&oacute;rego dosz&#322;o 11 lipca, Majski zadeklarowa&#322;, &#380;e przeka&#380;e swemu rz&#261;dowi polskie postulaty uj&#281;te w pi&#281;ciu punktach: &bdquo;1. Rz&#261;d radziecki wypowie traktaty z Niemcami, z sierpnia i wrze&#347;nia 1939 r.; 2. Nast&#261;pi normalizacja stosunk&oacute;w mi&#281;dzy obydwoma rz&#261;dami i akredytacja polskiego ambasadora w Moskwie; 3. Polska b&#281;dzie gotowa wsp&oacute;&#322;pracowa&#263; we wsp&oacute;lnej walce przeciwko Niemcom; 4. Utworzenie specjalnej Armii, jako oddzielnej jednostki na terytorium radzieckim, kt&oacute;ra mog&#322;aby by&#263; przeniesiona gdzie indziej, je&#347;liby sobie tego strona polska &#380;yczy&#322;a. Dok&#322;adny status Armii Polskiej odpowiada&#322;by statusowi Polskich Si&#322; Zbrojnych w Zjednoczonym Kr&oacute;lestwie Wielkiej Brytanii i P&oacute;&#322;nocnej Irlandii; 5. Zwolnienie internowanych &#380;o&#322;nierzy polskich i wi&#281;&#378;ni&oacute;w politycznych&rdquo;.</span>
          </p>
          <p class="c1"><span class="c2">Podpisanie uk&#322;adu, Londyn, 30 lipca 1941. Od lewej: W&#322;adys&#322;aw Sikorski, Anthony Eden, Winston Churchill i Iwan Majski. </span>
          </p>
          <p class="c1"><span class="c2">Znamienne, &#380;e nie znalaz&#322; si&#281; tutaj postulat dotycz&#261;cy granic, o kt&oacute;rym genera&#322; Sikorski m&oacute;wi&#322; w przem&oacute;wieniu radiowym. Wywo&#322;a&#322;o to burz&#281; na posiedzeniu rz&#261;du RP. Do dymisji poda&#322;o si&#281; trzech jego ministr&oacute;w: genera&#322; Kazimierz Sosnkowski, August Zaleski i Marian Seyda. Negatywne stanowisko wyrazi&#322; r&oacute;wnie&#380; Prezydent RP W&#322;adys&#322;aw Raczkiewicz. Prezydent nawo&#322;ywa&#322; do zerwania rozm&oacute;w z Sowietami i zadeklarowa&#322;, &#380;e nie podpisze umowy w takim kszta&#322;cie, co zreszt&#261; p&oacute;&#378;niej uczyni&#322;. W prote&#347;cie dosz&#322;o r&oacute;wnie&#380; do rozwi&#261;zania Rady Narodowej RP, pe&#322;ni&#261;cej rol&#281; polskiego parlamentu na uchod&#378;stwie. Kryzys gabinetowy nie zmusi&#322; jednak genera&#322;a Sikorskiego do przerwania rokowa&#324;, tym bardziej &#380;e poddany by&#322; on silnej presji ze strony Brytyjczyk&oacute;w. Na Sikorskiego, o czym dzi&#347; si&#281; zapomina, naciska&#322; r&oacute;wnie&#380; mocno rz&#261;d czechos&#322;owacki, co zwi&#261;zane by&#322;o z rozmowami o przysz&#322;ej &bdquo;polsko-czechos&#322;owackiej unii&rdquo; po zako&#324;czeniu wojny.</span>
          </p>
          <p class="c1"><span
                  class="c3">Mimo braku potwierdzenia expressis verbis granicy polsko-sowieckiej ustalonej </span><span
                  class="c3"><a class="c8"
                                href="https://www.google.com/url?q=http://www.polska-zbrojna.pl/home/articleshow/27811?t%3DWizja-marszalka-prysla-w-Rydze&amp;sa=D&amp;source=editors&amp;ust=1619026199501000&amp;usg=AOvVaw3ap2W1cFCjTg2iI_D13Wsd">w traktacie ryskim</a></span><span
                  class="c3">, genera&#322; Sikorski 30 lipca 1941 roku zdecydowa&#322; si&#281; parafowa&#263; pakt polsko-sowiecki, kt&oacute;ry od nazwisk sygnatariuszy przeszed&#322; do historii jako pakt lub uk&#322;ad Sikorski-Majski. Wobec odmowy prezydenta Raczkiewicza podpisania go, formalnie pozostawa&#322; on zwyk&#322;ym protoko&#322;em. Nie og&#322;oszono go r&oacute;wnie&#380; w Dzienniku Ustaw, ani nie zarejestrowano w Sekretariacie Ligi Narod&oacute;w. W tamtym momencie mia&#322;o to jednak drugorz&#281;dne znaczenie. Sowieci w pierwszym punkcie uk&#322;adu uznali za nieby&#322;e traktaty sowiecko-niemieckie z 1939 roku; w drugim potwierdzono nawi&#261;zanie wzajemnych stosunk&oacute;w dyplomatycznych (polski ambasador w ZSRS Stanis&#322;aw Kot przyby&#322; do Moskwy 4 wrze&#347;nia 1941 roku); w trzecim obie strony zadeklarowa&#322;y wzajemn&#261; pomoc w wojnie z Niemcami; w czwartym </span><span
                  class="c3"><a class="c8"
                                href="https://www.google.com/url?q=http://polska-zbrojna.pl/home/articleshow/25972?t%3D75-lat-temu-powstal-2-Korpus-Polski&amp;sa=D&amp;source=editors&amp;ust=1619026199502000&amp;usg=AOvVaw2g3hUtWXkpZ7Fnk1I6TySn">rz&#261;d sowiecki zgadza&#322; si&#281; na formowanie na swym terytorium polskiej armii</a></span><span
                  class="c2">. W dodatkowym protokole do paktu Sowieci zobowi&#261;zywali si&#281;, &#380;e po przywr&oacute;ceniu stosunk&oacute;w dyplomatycznych udzielona zostanie amnestia wszystkim obywatelom polskim pozbawionym wolno&#347;ci w ZSRS.</span>
          </p>
          <p class="c1"><span class="c2">Przeciwnicy podpisania uk&#322;adu wytykali genera&#322;owi Sikorskiemu, &#380;e zaprzepa&#347;ci&#322; granice wschodnie. Podkre&#347;lali nie bez racji, &#380;e m&oacute;g&#322; bardziej twardo negocjowa&#263; z Sowietami, kt&oacute;rzy w lipcu byli w rozpaczliwej sytuacji na froncie i przez to sk&#322;onni do ust&#281;pstw jak nigdy wcze&#347;niej. Prof. Pawe&#322; Wieczorkiewicz przywo&#322;uje &#347;wiadectwo genera&#322;a Mariana Januszajtisa-&#379;egoty, kt&oacute;ry w czasie kiedy toczy&#322;y si&#281; polsko-sowieckie rozmowy przebywa&#322; jeszcze w wi&#281;zieniu na &#321;ubiance. O negocjacjach dowiedzia&#322; si&#281; od samego &#321;awrentija Berii. Szef NKWD w czasie przes&#322;ucha&#324; zaprosi&#322; genera&#322;a do dyskusji na ten temat. Prof. Wieczorkiewicz zauwa&#380;y&#322;: &bdquo;Januszajtis s&#261;dzi&#322;, &#380;e Sikorski w &#380;adnym wypadku nie powinien zgodzi&#263; si&#281; na podpisanie uk&#322;adu bez gwarancji granicznych &ndash; a tak go podpisano. Beria twierdzi&#322;, &#380;e Polacy jednak ten uk&#322;ad podpisz&#261; i potem triumfowa&#322;, &#380;e tak w&#322;a&#347;nie si&#281; sta&#322;o. O&#347;wiadczy&#322; [&hellip;], &#380;e Sowieci podpisaliby gwarancje graniczne, bo znajdowali si&#281; w sytuacji przymusu. Sikorski nie wyczu&#322; tego momentu&rdquo;. Jednak profesor od razu zauwa&#380;y&#322;, &#380;e je&#347;li dosz&#322;oby do gwarancji granicznych, to i tak Stalin m&oacute;g&#322;by je potem z&#322;ama&#263; &ndash; nie takie umowy Sowieci &#322;amali. Poza tym polski premier nie mia&#322; w tej mierze &#380;adnego wsparcia ze strony zachodnich sojusznik&oacute;w.</span>
          </p>
          <p class="c1"><span class="c2">Brytyjski rz&#261;d podkre&#347;la&#322; ca&#322;y czas, &#380;e gwarantowa&#322; Rzeczypospolitej jedynie niepodleg&#322;o&#347;&#263;, a nie granice. Podobne stanowisko zaj&#281;li Amerykanie. Sumner Welles dzie&#324; po podpisaniu polsko-sowieckiego paktu wyrazi&#322; si&#281; o nim entuzjastycznie, ale w jego wypowiedzi nie pojawi&#322;o si&#281; ani jedno s&#322;owo o wschodniej granicy Polski.</span>
          </p>
          <p class="c1"><span class="c4">Wyrwani z nieludzkiej ziemi</span><span class="c2">&nbsp;</span></p>
          <p class="c1" id="h.gjdgxs"><span class="c2">Jednak niezaprzeczalnym sukcesem Sikorskiego by&#322; ten dodatkowy protok&oacute;&#322; do uk&#322;adu, w kt&oacute;rym by&#322;a mowa o amnestii polskich obywateli w ZSRS. Tym rzeczywi&#347;cie ocali&#322; tysi&#261;ce Polak&oacute;w nie tylko nadal wi&#281;zionych po niemieckim naje&#378;dzie, ale i mordowanych ka&#380;dego dnia w czasie ewakuacji wi&#281;zie&#324; NKWD w g&#322;&#261;b sowieckiego pa&#324;stwa. Jednak samo s&#322;owo &bdquo;amnestia&rdquo; wywo&#322;a&#322;o w&#347;r&oacute;d Polak&oacute;w na Zachodzie wielkie kontrowersje i oburzenie. Podkre&#347;lano, &#380;e ani je&#324;cy wojenni, ani deportowani czy wi&#281;zieni Polacy w Zwi&#261;zku Sowieckim nie pope&#322;nili &#380;adnych przest&#281;pstw, kt&oacute;re upowa&#380;nia&#322;yby w&#322;adze sowieckie do ich amnestionowania. Jednak najwa&#380;niejsze by&#322;o to, &#380;e dano tym ludziom szans&#281; wydostania si&#281; z sowieckiego piek&#322;a. Jak uj&#261;&#322; to Karol Popiel (chadecki polityk, na uchod&#378;stwie): &bdquo;Ponad nieskazitelno&#347;&#263; formu&#322;y prawnej wysuwa si&#281; sprawa losu gin&#261;cych na olbrzymich obszarach Zwi&#261;zku Sowieckiego mas zes&#322;a&#324;c&oacute;w, je&#324;c&oacute;w i wi&#281;&#378;ni&oacute;w&rdquo;. Mo&#380;na dyskutowa&#263;, czy genera&#322; Sikorski nie m&oacute;g&#322; przeci&#261;ga&#263; negocjacji, ale nale&#380;y pami&#281;ta&#263;, &#380;e z ka&#380;d&#261; godzin&#261;, ka&#380;dym dniem na nieludzkiej ziemi gin&#281;li kolejni Polacy. Krytycy genera&#322;a wychodzili wtedy r&oacute;wnie&#380; z za&#322;o&#380;enia, &#380;e godziny Armii Czerwonej s&#261; ju&#380; policzone i z Sowietami trzeba rozmawia&#263; twardo i bez ust&#281;pstw. Kolejne lata wojny pokaza&#322;y, &#380;e si&#281; mylili, a genera&#322; W&#322;adys&#322;aw Anders m&oacute;g&#322; wyprowadzi&#263; z sowieckiej Rosji zar&oacute;wno sw&#261; armi&#281;, jaki i tysi&#261;ce cywil&oacute;w, kt&oacute;rzy niechybnie by tam wymarli, gdyby nie pakt Sikorski-Majski. W dyskusji o nim zawsze nale&#380;y mie&#263; w pami&#281;ci s&#322;owa cytowanego na pocz&#261;tku Antoniego &#321;api&#324;skiego i wielu innych, kt&oacute;rym uda&#322;o si&#281; prze&#380;y&#263; to piek&#322;o i p&oacute;&#378;niej walczy&#263; i &#380;y&#263; dla Polski.</span>
          </p>
          <p class="c1"><span class="c4">Bibliografia</span><span class="c2">&nbsp;</span></p>
          <p class="c1"><span class="c0">E. Kospath-Paw&#322;owski, Wojsko Polskie na Wschodzie 1943&ndash;1945, Pruszk&oacute;w 1993<br>K. Popiel, Gen. Sikorski w mojej pami&#281;ci, Londyn 1978<br>P. &#379;aro&#324;, Kierunek wschodni w strategii wojskowo-politycznej gen. W&#322;adys&#322;awa Sikorskiego 1940&ndash;1943, Warszawa 1988<br>Uk&#322;ad Sikorski-Majski. Z prof. Paw&#322;em Wieczorkiewiczem rozmawia Wojciech Koz&#322;owski, https://muzhp.pl/pl/e/25/uklad-sikorski-majski (stan na lipiec 2019)&nbsp;</span>
          </p>
          <p class="c9"><span class="c0"></span></p>
        </blockquote>
      </div>
