      <div class="col ps-4 pe-1 py-2 text-start">
        <blockquote>
          <p class="c4"><span
                  class="c7 c8">Krew na &#347;niegu &ndash; rozpoczyna si&#281; bitwa o Monte Cassino</span></p>
          <p class="c4 c10"><span class="c7 c3"></span></p>
          <p class="c4" id="h.gjdgxs"><span class="c3 c7">Piotr Korczy&#324;ski</span></p>
          <p class="c0"><span class="c3">Bitw&#281; o masyw Monte Cassino uwa&#380;a si&#281; powszechnie za najkrwawsze starcie na froncie zachodnim II wojny &#347;wiatowej. Bilans walk nie by&#322;by tak straszny, gdyby na ich pocz&#261;tku alianccy dow&oacute;dcy pami&#281;tali o naukach Hannibala i Napoleona i nie pope&#322;niali tak karygodnych b&#322;&#281;d&oacute;w, jak chocia&#380;by zbombardowanie klasztoru na Monte Cassino 15 lutego 1944 roku.</span><span
                  class="c2">&nbsp;</span></p>
          <p class="c0"><span class="c2">Po sukcesach wojsk alianckich w Afryce P&oacute;&#322;nocnej postanowiono i&#347;&#263; za ciosem i rozpocz&#261;&#263; walk&#281; po drugiej stronie Morza &#346;r&oacute;dziemnomorskiego. W nocy z 9 na 10 lipca 1943 roku na pla&#380;ach Sycylii wyl&#261;dowali &#380;o&#322;nierze ameryka&#324;scy, brytyjscy, kanadyjscy i australijscy. Niemcy i W&#322;osi byli kompletnie zaskoczeni, ale ich obrona szybko si&#281; pozbiera&#322;a i sprzymierzeni dopiero na pocz&#261;tku wrze&#347;nia mogli przeskoczy&#263; z Sycylii na po&#322;udniowy kraniec &bdquo;w&#322;oskiego buta&rdquo;. Desant pod Salerno 9 wrze&#347;nia 1943 roku os&#322;ania&#322;y mi&#281;dzy innymi polskie niszczyciele i okr&#281;ty podwodne. Szczeg&oacute;lnie ws&#322;awi&#322; si&#281; w&#347;r&oacute;d nich ORP &bdquo;Dzik&rdquo;, kt&oacute;ry w ci&#261;gu dw&oacute;ch tygodni patrolowania Morza Liguryjskiego storpedowa&#322; 14 nieprzyjacielskich okr&#281;t&oacute;w.</span>
          </p>
          <p class="c0"><span class="c2">L&#261;dowanie sprzymierzonych w po&#322;udniowych W&#322;oszech by&#322;o nie tylko sukcesem militarnym, lecz tak&#380;e prze&#322;omem politycznym &ndash; W&#322;osi obalili faszystowski rz&#261;d Benito Mussoliniego i przeszli na stron&#281; aliant&oacute;w. Adolf Hitler nie zamierza&#322; jednak darowa&#263; W&#322;ochom tej zdrady, ani pozostawi&#263; w opa&#322;ach swego &bdquo;faszystowskiego guru&rdquo; Mussoliniego. Kiedy 8 wrze&#347;nia W&#322;osi og&#322;osili oficjalnie kapitulacj&#281;, 40 niemieckich dywizji zaj&#281;&#322;o ca&#322;y kraj, a wi&#281;kszo&#347;&#263; sojuszniczej do tej pory armii w&#322;oskiej rozbrojono i zamkni&#281;to za drutami oboz&oacute;w jenieckich.</span>
          </p>
          <p class="c0"><span class="c3">Droga &#347;limaka po brzytwie</span><span class="c2">&nbsp;</span></p>
          <p class="c0"><span class="c2">Cz&#322;owiekiem, kt&oacute;ry mia&#322; udowodni&#263; aliantom, &#380;e l&#261;duj&#261;c we W&#322;oszech wst&#261;pili jednocze&#347;nie do piek&#322;a, zosta&#322; feldmarsza&#322;ek Luftwaffe Albert Kesserling. Dowodzi&#322; on najbitniejszymi &#380;o&#322;nierzami w armii niemieckiej &ndash; strzelcami spadochronowymi, zwanymi przez nieprzyjaci&oacute;&#322; &bdquo;zielonymi diab&#322;ami&rdquo;. W tym czasie spadochroniarze niemieccy nie mieli ju&#380; wielu okazji do desant&oacute;w spadochronowych, lecz jako doborowa piechota okazali si&#281; &#347;wietni w obronie, o czym mieli si&#281; przekona&#263; alianci, m.in. pod Monte Cassino. Kesserling, by powstrzyma&#263; marsz sprzymierzonych do Rzymu, stworzy&#322; na tym kierunku kilka pozycji obronnych: zimow&#261;, Bernhardta i Gustawa. Ta ostatnia opiera&#322;a si&#281; na masywie Monte Cassino, kt&oacute;ry by&#322; postrachem dla wszystkich armii atakuj&#261;cych Itali&#281; od czas&oacute;w staro&#380;ytnych. To m.in. s&#322;ynny w&oacute;dz Kartagi&#324;czyk&oacute;w, Hannibal, wola&#322; i&#347;&#263; okr&#281;&#380;n&#261; drog&#261; przez Alpy i wytraci&#263; tam prawie wszystkie swoje s&#322;onie, ni&#380; nara&#380;a&#263; si&#281; na pewn&#261; kl&#281;sk&#281; u &bdquo;wr&oacute;t rygluj&#261;cych Rzym&rdquo;. Natomiast zdobywca W&#322;och, Napoleon Bonaparte, da&#322; swoim ewentualnym na&#347;ladowcom prost&#261; rad&#281;: &bdquo;W&#322;ochy to but. Trzeba wchodzi&#263; w nie od g&oacute;ry&rdquo;. W sztabie armii sprzymierzonych o tej przestrodze pami&#281;ta&#322; jedynie rodak cesarza Francuz&oacute;w, dow&oacute;dca Francuskiego Korpusu Ekspedycyjnego, genera&#322; Alfons Juin. Znaj&#261;c przebieg w&#322;oskiej kampanii Napoleona, Juin by&#322; prawie pewien, &#380;e alianckie natarcie w kierunku Rzymu zostanie zatrzymane w rejonie masywu Monte Cassino. Dlatego proponowa&#322; g&#322;&oacute;wnodowodz&#261;cemu sprzymierzonych, gen. Dwightowi D. Eisenhowerowi, by zamiast wik&#322;a&#263; si&#281; w walki na po&#322;udniu W&#322;och, przeprowadzi&#263; desanty na p&oacute;&#322;nocy &ndash; pod Livorno i Ankon&#261;, czyli wej&#347;&#263; we &bdquo;w&#322;oski but&rdquo; od g&oacute;ry. Niestety, tej m&#261;drej rady nie chcia&#322; s&#322;ucha&#263; ani Eisenhower, ani inni anglosascy dow&oacute;dcy. To dowodzi&#322;o wyra&#378;nie, &#380;e alianccy sztabowcy nie zdawali sobie sprawy z przeszk&oacute;d terenowych, jakie tam b&#281;d&#261; na nich czeka&#263;. Ponadto Italia kojarzy&#322;a im si&#281; z turystycznymi folderami &bdquo;s&#322;onecznego raju&rdquo; i nie brali pod uwag&#281;, &#380;e zima i wiosna zmieni w&#322;oskie drogi w bagno, a wezbrane rzeki w miejsca nie do przebycia. Ponadto, &#380;o&#322;nierze feldmarsza&#322;ka Kesserlinga &#347;wietnie wykonali sapersk&#261; prac&#281;, przygotowuj&#261;c umocnienia polowe wymienionych linii obronnych. Warto wspomnie&#263;, &#380;e Niemcy oparli si&#281; przy tym na umocnieniach polowych Wojska Polskiego, kt&oacute;re przysz&#322;o im z wielkim trudem prze&#322;amywa&#263; we wrze&#347;niu 1939 roku.</span>
          </p>
          <p class="c0"><span class="c2">To wszystko spowodowa&#322;o, &#380;e ofensywa aliancka wyra&#378;nie wyhamowa&#322;a w pa&#378;dzierniku 1943 roku. Kolejne miesi&#261;ce na P&oacute;&#322;wyspie Apeni&#324;skim by&#322;y ju&#380; dla sprzymierzonych powolnym &bdquo;posuwaniem si&#281; &#347;limaka po ostrzu brzytwy&rdquo;. Na pocz&#261;tku listopada 1943 roku po ci&#281;&#380;kich walkach alianci przesun&#281;li si&#281; na kolejn&#261; niemieck&#261; lini&#281; obrony na rzece Rapido, czyli pozycj&#281; Bernhardta. Nast&#281;pnie dotarli do miasteczka Cassino i do podn&oacute;&#380;a masywnego wzg&oacute;rza Monte Cassino, na kt&oacute;rym zobaczyli mury s&#322;ynnego benedykty&#324;skiego opactwa. Niestety, nie mieli zbyt wiele czasu na podziwianie jednego z architektonicznych arcydzie&#322;, gdy&#380; ze wzg&oacute;rza lun&#261;&#322; na nich ogie&#324; z niemieckich dzia&#322; i karabin&oacute;w maszynowych. Okaza&#322;o si&#281;, &#380;e strzelcy spadochronowi rygluj&#261; nim jedyn&#261; drog&#281; pozwalaj&#261;c&#261; na swobodne przemieszczanie si&#281; wojsk &ndash; szos&#281; biegn&#261;c&#261; wzd&#322;u&#380; wybrze&#380;a w kierunku Rzymu. Tutaj definitywnie sko&#324;czy&#322;y si&#281; sukcesy aliant&oacute;w z drugiej po&#322;owy roku 1943.</span>
          </p>
          <p class="c0"><span class="c3">&#379;ar&#322;oczne g&oacute;ry</span><span class="c2">&nbsp;</span></p>
          <p class="c0"><span class="c2">W opisywanym okresie, czyli na prze&#322;omie 1943 i 1944 roku, dzia&#322;ania na kolejnych liniach niemieckiej obrony we W&#322;oszech nabiera&#322;y charakteru walk pozycyjnych, tak dobrze znanych z czas&oacute;w I wojny &#347;wiatowej czy niekt&oacute;rych kampanii na froncie wschodnim w II wojnie &#347;wiatowej. Kiedy g&#322;&oacute;wne si&#322;y walcz&#261;cych stron tkwi&#322;y d&#322;u&#380;szy czas naprzeciw siebie, strzelaj&#261;c od czasu do czasu z artylerii i cekaem&oacute;w, rozpocz&#281;&#322;a si&#281; &bdquo;robota patrolowa&rdquo;. Tak te&#380; by&#322;o nad rzekami Garigliano i Rapido, a jednymi z najlepszych zwiadowc&oacute;w alianckich okazali si&#281; polscy komandosi z 1 Samodzielnej Kompanii Commando, dowodzonej przez mjr. W&#322;adys&#322;awa Smrokowskiego. Kompania od 12 grudnia 1943 roku walczy&#322;a we w&#322;oskich g&oacute;rach w sk&#322;adzie brytyjskiej 2 Brygady do Zada&#324; Specjalnych gen. Toma Churchilla. &#379;o&#322;nierze majora Smrokowskiego odznaczyli si&#281; m.in. rajdem na wzg&oacute;rze Monte Valle Martina, kt&oacute;re zdobyli razem z Brytyjczykami z Queens Regiment 17 stycznia 1944 roku, ale ten sukces nie czyni&#322; jednak wiosny w obozie alianckim w nowym 1944 roku. Pod koniec stycznia polscy komandosi ze sporymi stratami w zabitych i rannych zostali wycofani z frontu i nied&#322;ugo potem w&#322;&#261;czeni do 2 Korpusu Polskiego.</span>
          </p>
          <p class="c0"><span class="c2">Drobne sukcesy alianckich komandos&oacute;w czy zwiadowc&oacute;w nie mog&#322;y przys&#322;oni&#263; prawdy, &#380;e sytuacja wojsk sprzymierzonych u podn&oacute;&#380;a Monte Cassino jest z&#322;a. Pierwsza pr&oacute;ba zdobycia wzg&oacute;rza zosta&#322;a podj&#281;ta ju&#380; 5 listopada 1943 roku przez brytyjsk&#261; 56 Dywizj&#281; Piechoty i ameryka&#324;sk&#261; 3 Dywizj&#281; Piechoty. Atak zako&#324;czy&#322; si&#281; niepowodzeniem. R&oacute;wnocze&#347;nie brytyjska 8 Armia gen. Bernarda L. Montgomery&rsquo;ego podj&#281;&#322;a ofensyw&#281; po wschodniej stronie P&oacute;&#322;wyspu Apeni&#324;skiego, ale r&oacute;wnie&#380; tutaj wojska niewiele posun&#281;&#322;y si&#281; naprz&oacute;d poza niemieck&#261; pozycj&#281; zimow&#261;, ku linii Bernharda nad rzek&#261; Sangro. Tymczasem pod koniec listopada kolejn&#261; pr&oacute;b&#281; prze&#322;amania obrony masywu Monte Cassino podj&#281;&#322;a ameryka&#324;ska 5 Armia gen. Marka W. Clarka. Po morderczych walkach w grudniu uda&#322;o si&#281; zdoby&#263; wzg&oacute;rze Monte Camino, a pod koniec stycznia 1944 roku Monte Samourco i Monte Luka. Wszystko to Amerykanie okupili ogromnymi stratami. Grafika z niemieckiej ulotki propagandowej, na kt&oacute;rej w&#322;oskie g&oacute;ry z paszczami pe&#322;nymi z&#281;b&oacute;w jak u rekin&oacute;w po&#380;eraj&#261; ca&#322;e oddzia&#322;y anglosaskie, sta&#322;a si&#281; rzeczywisto&#347;ci&#261;.</span>
          </p>
          <p class="c0"><span class="c2">Wobec hekatomby w&#322;asnych oddzia&#322;&oacute;w alianccy dow&oacute;dcy postanowili podj&#261;&#263; pr&oacute;b&#281; niespodziewanego wej&#347;cia na ty&#322;y niemieckich linii obronnych. W operacji &bdquo;Single&rdquo; (&bdquo;&#379;wir&rdquo;) 22 stycznia 1944 roku wysadzono silny desant w rejonie Anzio-Nettuno, oko&#322;o 50 kilometr&oacute;w na po&#322;udniowy wsch&oacute;d od Rzymu. Niemcy zostali kompletnie zaskoczeni, ale zamiast ruszy&#263; szybko do przodu, dowodz&#261;cy si&#322;ami desantowymi ameryka&#324;ski genera&#322; John P. Lucas zacz&#261;&#322; umacnia&#263; przycz&oacute;&#322;ek na pla&#380;ach. To da&#322;o czas feldmarsza&#322;kowi Kesserlingowi na wzmocnienie obrony pod Anzio, a Amerykanie utkn&#281;li tam na dobre &ndash; dok&#322;adnie na cztery miesi&#261;ce. Gen. Lucas za swoje &bdquo;zas&#322;ugi&rdquo; zosta&#322; zwolniony z dow&oacute;dztwa i odes&#322;any do Stan&oacute;w Zjednoczonych. Niestety, po zaprzepaszczeniu tej szansy znowu trzeba by&#322;o planowa&#263; kolejne natarcie na masyw Monte Cassino, kt&oacute;ry Niemcy po skr&oacute;ceniu swych linii obrony nazwali pozycj&#261; Gustawa.</span>
          </p>
          <p class="c0"><span class="c3">Najwi&#281;kszy b&#322;&#261;d w&#322;oskiej kampanii </span></p>
          <p class="c0"><span class="c2">Po wykrwawieniu si&#281; Amerykan&oacute;w w kolejnym ataku na Monte Cassino, na pocz&#261;tku lutego 1944 roku, to zadanie, wydawa&#322;oby si&#281; niewykonalne, otrzymali Nowozelandczycy pod dow&oacute;dztwem gen. Bernarda Freyberga. Po wielu naradach w sztabie Freyberg o&#347;wiadczy&#322;, &#380;e nie po&#347;le swych ludzi do walki, p&oacute;ki nie zostanie zbombardowany klasztor Benedyktyn&oacute;w na wzg&oacute;rzu Monte Cassino. Nowozelandczyk, wyczytawszy w jakim&#347; opracowaniu popularnonaukowym, &#380;e przez wieki by&#322;a to twierdza nie do zdobycia, uzna&#322;, &#380;e klasztor jest najwi&#281;kszym problemem, kt&oacute;ry stoi na drodze do sukcesu jego wojsk. Nie by&#322;a to jednak tylko i wy&#322;&#261;cznie twierdza, lecz przede wszystkim jedna z kolebek chrze&#347;cija&#324;stwa, w kt&oacute;rej murach zgromadzono wielkie dzie&#322;a sztuki i pi&#347;miennictwa, a obie walcz&#261;ce strony deklarowa&#322;y, &#380;e w swych dzia&#322;aniach wojennych b&#281;d&#261; chroni&#263; europejskie dziedzictwo kulturowe na terenie W&#322;och. Z tego te&#380; powodu pochodz&#261;cy z katolickiej, bawarskiej rodziny Kesserling (sk&#261;din&#261;d niewahaj&#261;cy si&#281; w 1939 roku barbarzy&#324;sko bombardowa&#263; Warszawy) wzbrania&#322; si&#281; przed fortyfikowaniem klasztoru. Ostatecznie Amerykanie zezwolili na bombardowanie, ale wcze&#347;niej poinformowali o tym zakonnik&oacute;w i Stolic&#281; Apostolsk&#261;. Dzi&#281;ki Niemcom ewakuowano z klasztoru ludzi i cenne zbiory.</span>
          </p>
          <p class="c0"><span class="c2">Rankiem 15 lutego 1944 roku 255 ameryka&#324;skich &bdquo;lataj&#261;cych fortec&rdquo; zrzuci&#322;o na Monte Cassino ponad 350 ton bomb burz&#261;cych, kt&oacute;re zdmuchn&#281;&#322;y grube mury klasztoru niczym domek z kart. Jak wielkim b&#322;&#281;dem by&#322; ten nalot, by nie powiedzie&#263; &ndash; g&#322;upot&#261;, genera&#322; Freyberg przekona&#322; si&#281; bardzo szybko. Jego waleczni Maorysi i Gurkhowie, mimo pogardy &#347;mierci, z kt&oacute;rej s&#322;yn&#281;li, nie zdo&#322;ali wyrzuci&#263; z bunkr&oacute;w niemieckich strzelc&oacute;w spadochronowych ani 17 lutego, ani w czasie kolejnego krwawego natarcia &ndash; 15 marca 1944 roku. Spadochroniarze, zaj&#261;wszy zbombardowane ruiny klasztoru, uczynili z niego prawdziw&#261; twierdz&#281; nie do zdobycia. Dow&oacute;dca si&#322; alianckich we W&#322;oszech, gen. Harold Alexander, stwierdzi&#322; wtedy: &bdquo;Niestety, walczymy z najlepszymi &#380;o&#322;nierzami na &#347;wiecie&rdquo;. Natomiast dow&oacute;dca 5 Armii, gen. Mark Clark, przyzna&#322; po wojnie bez ogr&oacute;dek: &bdquo;Zbombardowanie klasztoru stanowi&#322;o nie tylko niepotrzebny b&#322;&#261;d psychologiczny z propagandowego punktu widzenia, by&#322;o te&#380; z wojskowego punktu widzenia b&#322;&#281;dem pierwszej klasy. Utrudni&#322;o tylko zadanie i zwi&#281;kszy&#322;o straty w ludziach i sprz&#281;cie, przynosz&#261;c tak&#380;e strat&#281; czasu&rdquo;. Historycy do dzi&#347; dyskutuj&#261; nad tym fenomenem lekkomy&#347;lno&#347;ci, a niekt&oacute;rzy posuwaj&#261; si&#281; nawet do tak daleko id&#261;cych wniosk&oacute;w, jak ten, &#380;e by&#322; to przejaw odwiecznej rywalizacji obozu protestanckiego (Anglik&oacute;w i Amerykan&oacute;w) z papistami.</span>
          </p>
          <p class="c0"><span class="c2">Abstrahuj&#261;c od tych kontrowersyjnych teorii, nale&#380;y stwierdzi&#263;, &#380;e w maju 1944 roku do zdobycia ruin Monte Cassino ruszyli &bdquo;papi&#347;ci&rdquo;, a w si&#322;ach sprzymierzonych &ndash; Polacy. Kiedy bowiem na prze&#322;omie 1943 i 1944 roku we w&#322;oskich g&oacute;rach prowadzili swe rajdy komandosi z polskiej 1 Samodzielnej Kompanii Commando, w Tarencie na po&#322;udniu W&#322;och, ze statk&oacute;w i okr&#281;t&oacute;w schodzi&#322;y na l&#261;d pierwsze oddzia&#322;y 2 Korpusu Polskiego gen. W&#322;adys&#322;awa Andersa. Ju&#380; nied&#322;ugo i jego &#380;o&#322;nierze mieli wzi&#261;&#263; udzia&#322; w bitwie najlepszych &#380;o&#322;nierzy na &#347;wiecie.</span>
          </p>
          <p class="c0"><span class="c3">Bibliografia:</span><span class="c2">&nbsp;</span></p>
          <p class="c0"><span class="c5">P. Caddick-Adams, &bdquo;Monte Cassino. Piek&#322;o dziesi&#281;ciu armii&rdquo;, Krak&oacute;w 2014.</span>
          </p>
          <p class="c0"><span class="c5">Z. Wawer, &bdquo;Monte Cassino 1944&rdquo;, Warszawa 2009.</span></p>
          <p class="c0"><span class="c5">P. Korczy&#324;ski, &bdquo;Elitarne oddzia&#322;y specjalne II wojny &#347;wiatowej 1939&ndash;1945&rdquo;, Pozna&#324; 2013.</span>
          </p>
          <p class="c0"><span class="c5">M. Fiszer, J. Gruszczy&#324;ski, &bdquo;Aliancka ofensywa na Rzym: stycze&#324;&ndash;czerwiec 1944 r.&rdquo;, [w:] &bdquo;Monte Cassino 1944&rdquo;, &bdquo;Technika Wojskowa Historia&rdquo; 2014 nr 2, marzec-kwiecie&#324;, s. 34&ndash;47.</span>
          </p>
          <p class="c0"><span class="c5">Z. Wawer, &bdquo;Zdoby&#263; Monte Cassino&rdquo;, [w:] &bdquo;Monte Cassino 1944&rdquo;, &bdquo;Technika Wojskowa Historia&rdquo; 2014 nr 2, marzec-kwiecie&#324; 2014, s. 58&ndash;74.</span>
          </p>
          <p class="c6"><span class="c7 c9"></span></p>
        </blockquote>
      </div>
