<template>
  <div class="header-menu">
    <div id="myNav" class="overlay">
      <a class="closebtn" @click="closeNav()">&times;</a>
      <div class="overlay-content">
        <div class="d-flex">
          <ul class="list-inline mx-auto justify-content-center">
            <li class="list-item text-start" v-for="page in topPages" :key="page.title">
              <a class="nav-link link-size link-color" @click="openSubmenu(page)">
                {{ page.title }}
              </a>
              <div v-if="isDropdown(page)"
                   :class="isDropdown(page) +' mx-4 display-none'" id="monte-cassino">
                <a v-for="subpage in page.subpages" :key="subpage"
                   class="nav-link link-size link-color">
                  {{ subpage.title }}
                </a>
              </div>
            </li>
          </ul>
        </div>
      </div>
    </div>

    <button class="navbar-toggler position-fixed" @click="openNav()">
      <i class="fas fa-bars toogler-icon"></i>
    </button>
    <div v-if="!isHomePage()">
      <a @click="$router.back()" class="btn navbar-toggler top-5rem btn-warning text-white position-fixed">
        Powrót
      </a>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      topPages: [
        {
          title: 'Dojazd do szlaku, do Cassino, parkingi',
          subpages: []
        },
        {
          title: 'Przewodnicy współpracujący przy ścieżce',
          subpages: []
        },
        {
          title: 'Hotele wspierające projekt',
          subpages: []
        },
        {
          title: 'Informacje dla podróżnych (telefony, adresy, strony www)',
          subpages: []
        },
        {
          title: 'Ważne miejsca w Cassino',
          subpages: [
            {
              title: 'Pomnik Andersa'
            },
            {
              title: 'Cmentarz Polski'
            },
            {
              title: 'Klasztor na Monte Cassino'
            }
          ]
        },
      ],
    }
  },
  methods: {
    isDropdown(page) {
      return page.subpages.length === 0 ? '' : 'dropdown';
    },
    openNav() {
      document.getElementById("myNav").style.width = "100%";
    },
    closeNav() {
      document.getElementById("myNav").style.width = "0%";
    },
    openSubmenu(page) {
      let hasSubmenu = this.isDropdown(page);
      if (hasSubmenu) {
        document.getElementById("monte-cassino").classList.toggle('d-block');
      }
    },
    isHomePage() {
      return this.$route.path === '/';
    },
  }
}

</script>
<style lang="scss">

$tooglerColor: #cc7761;
$iconSize: 20px;
$topMenuBgColor: #364b51;

.top-5rem {
  top: 5rem !important;
}

#monte-cassino {
  transition: 1s;
}

.dropdown .nav-link {
  font-size: 1rem;
}

.display-none {
  display: none;
}

.navbar-toggler {
  color: $tooglerColor;
  border-color: $tooglerColor !important;
  z-index: 4;
  top: 1rem;
  right: 1rem;
}

.toogler-icon {
  color: $tooglerColor;
  font-size: $iconSize;
  z-index: 1;
}

.overlay {
  height: 100%;
  width: 0;
  position: fixed;
  z-index: 5;
  left: 0;
  top: 0;
  background-color: rgba(63, 76, 82, 0.97);
  overflow-x: hidden;
  transition: 0.5s;
}

.overlay-content {
  position: relative;
  top: 20%;
  width: 100%;
  text-align: center;
  margin-top: 30px;
}

.overlay a {
  padding: 8px;
  text-decoration: none;
  font-size: 1.5rem;
  color: #818181;
  display: block;
  transition: 0.3s;
  cursor: pointer;
}

.overlay a:focus {
  color: $tooglerColor;
}

.overlay .closebtn {
  position: absolute;
  top: 20px;
  right: 45px;
  font-size: 60px;
  cursor: pointer;
}

.closebtn:hover {
  color: $tooglerColor;
}

@media screen and (max-height: 450px) {
  .overlay a {
    font-size: 20px;
  }

  .overlay .closebtn {
    font-size: 40px;
    top: 15px;
    right: 35px;
  }
}

</style>