  <div class="container ">
    <div class="row">
      <div class="col ps-4 pe-1 py-2 text-start">
        <blockquote>
          <p class="c2"><span class="c9 c8">Sztandar na gruzach, w&#347;r&oacute;d chmur</span></p>
          <p class="c2 c6"><span class="c8 c9"></span></p>
          <p class="c0"><span class="c9 c8">Piotr Korczy&#324;ski</span></p>
          <p class="c0"><span class="c8">&#346;wit 18 maja 1944 roku zapowiada&#322; kolejny s&#322;oneczny dzie&#324;. Wymarzona pogoda. Ale nie wok&oacute;&#322; ruin klasztoru na Monte Cassino. Taka aura oznacza&#322;a, &#380;e w upale znowu przyjdzie gin&#261;&#263; dziesi&#261;tkom polskich &#380;o&#322;nierzy szturmuj&#261;cych to wzg&oacute;rze. Jednak ten ranek by&#322; inny. Nad pobojowiskiem panowa&#322;a cisza przerywana jedynie, od czasu do czasu, dalekim wystrza&#322;em armatnim.</span><span
                  class="c1">&nbsp;</span></p>
          <p class="c0"><span class="c3">Taka cisza by&#322;a w tym strasznym miejscu czym&#347; nienaturalnym. Za&#380;arte walki o wzg&oacute;rza, broni&#261;ce aliantom drogi do Rzymu, trwa&#322;y ju&#380; kilka d&#322;ugich miesi&#281;cy. Od stycznia 1944 roku wojska ameryka&#324;skie, brytyjskie, francuskie i nowozelandzkie stara&#322;y si&#281; wyrzuci&#263; doborowe oddzia&#322;y niemieckich strzelc&oacute;w spadochronowych i g&oacute;rskich z ich pozycji obronnych. Na pr&oacute;&#380;no. Niemcy dziesi&#261;tkowali &#380;o&#322;nierzy alianckich z precyzyjn&#261; regularno&#347;ci&#261;. Na nic zda&#322;a si&#281; tutaj przewaga lotnicza. Zbombardowanie klasztoru na </span><span
                  class="c3"><a class="c5"
                                href="https://www.google.com/url?q=http://www.polska-zbrojna.pl/home/articleshow/28265?t%3DPierwsze-polskie-walki-na-Monte-Cassino&amp;sa=D&amp;source=editors&amp;ust=1619038296806000&amp;usg=AOvVaw39uHfwVc3BdDNVqKJBPWum">Monte Cassino</a></span><span
                  class="c3">&nbsp;tylko uczyni&#322;o sytuacj&#281; niemieckich oddzia&#322;&oacute;w jeszcze korzystniejsz&#261; &ndash; w ruinach &#322;atwiej by&#322;o odpiera&#263; kolejne szturmy, a te za&#322;amywa&#322;y si&#281; jeden po drugim. Nawet najbitniejsi z bitnych &ndash; </span><span
                  class="c3"><a class="c5"
                                href="https://www.google.com/url?q=http://www.polska-zbrojna.pl/home/articleshow/28393?t%3DTowarzysze-broni-znad-Gangesu&amp;sa=D&amp;source=editors&amp;ust=1619038296807000&amp;usg=AOvVaw0zzCdA3WXyUFWhYCjDFKEw">Gurkhowie</a></span><span
                  class="c1">&nbsp;nie dali rady przebi&#263; si&#281; przez ogie&#324;&nbsp;&bdquo;zielonych diab&#322;&oacute;w&rdquo;, jak nazywano niemieckich spadochroniarzy.&nbsp;</span>
          </p>
          <p class="c0"><span class="c3">Kiedy 12 maja do ataku ruszyli &#380;o&#322;nierze </span><span class="c3"><a class="c5"
                                                                                                                       href="https://www.google.com/url?q=http://www.polska-zbrojna.pl/home/articleshow/25972?t%3D75-lat-temu-powstal-2-Korpus-Polski&amp;sa=D&amp;source=editors&amp;ust=1619038296807000&amp;usg=AOvVaw2eoNunTcjrbdaHpcumT8kE">2 Korpusu Polskiego</a></span><span
                  class="c1">, wygl&#261;da&#322;o na to, &#380;e kolejny raz potwierdzi si&#281; regu&#322;a znana od staro&#380;ytno&#347;ci, i&#380; te przekl&#281;te wzg&oacute;rza s&#261; nie do zdobycia. Istotnie, polskie natarcie za&#322;ama&#322;o si&#281;, ale to by&#322;o dopiero pierwsze podej&#347;cie. Polacy mieli z Niemcami znacznie wi&#281;ksze rachunki do wyr&oacute;wnania ni&#380; Anglosasi, Francuzi, Hindusi, Kanadyjczycy, Maroka&#324;czycy, Morysi czy Nowozelandczycy&hellip; Krwawili na swych wywalczonych pozycjach, ale trzymali je i 16 maja znowu ruszyli na Widmo, San Angelo, G&oacute;r&#281; Ofiarn&#261;&hellip; Niemcy te&#380; ju&#380; nie byli tak pewni swego, jak wcze&#347;niej. Dobrze znali swego odwiecznego wroga. I &#347;mier&#263; zacz&#281;&#322;a ich dziesi&#261;tkowa&#263; &ndash; polska &#347;mier&#263;&hellip; O tym wszystkim my&#347;la&#322; podporucznik Kazimierz Gurbiel, ch&#322;on&#261;c t&#281; nienaturaln&#261; cisz&#281;. Lecz w ko&#324;cu zm&#281;czenie wzi&#281;&#322;o g&oacute;r&#281; i oficer pogr&#261;&#380;y&#322; si&#281; w drzemce. Zar&oacute;wno on, jak i jego towarzysze z 12 Pu&#322;ku U&#322;an&oacute;w Podolskich, wspomagali w walce piechot&#281; ze swej karpackiej dywizji. Nie tylko szturmowali niemieckie bunkry, ale i prowadzili nieprzerwanie patrole bojowe, kiedy piechota przegrupowywa&#322;a si&#281; do kolejnego natarcia.</span>
          </p>
          <p class="c0"><span class="c8">Ostatni patrol</span><span class="c1">&nbsp;</span></p>
          <p class="c0"><span class="c1">Ze snu wyrwa&#322; podporucznika Gurbiela znajomy g&#322;os. To dow&oacute;dca jego szwadronu m&oacute;wi&#322;, &#380;e wzywa go porucznik Mieczys&#322;aw Sander. &bdquo;Pewnie znowu patrol, ta cisza jednak nie przynios&#322;a niczego nowego&rdquo; &ndash; pomy&#347;la&#322; Gurbiel, ale tym razem jego frontowe przeczucie by&#322;o niesp&oacute;jne. Mia&#322; to by&#263; zupe&#322;nie inny patrol ni&#380; poprzednie.</span>
          </p>
          <p class="c0"><span class="c1">Kiedy Gurbiel zameldowa&#322; si&#281; u porucznika, ten bez &#380;adnych wst&#281;p&oacute;w od razu wy&#322;uszczy&#322;, o co chodzi: &bdquo;Kaziu, wal do klasztoru. Spenetruj, co i jak. Powiadaj&#261;, &#380;e Niemc&oacute;w ju&#380; nie ma. Nie wierz. B&#261;d&#378; ostro&#380;ny jakby tam jeszcze siedzieli&rdquo;. Okaza&#322;o si&#281;, &#380;e nad ranem zauwa&#380;ono na ruinach klasztoru bia&#322;&#261; flag&#281;. Zelektryzowany takim rozkazem Gurbiel nie wr&oacute;ci&#322; po sw&oacute;j he&#322;m, bluz&#281; i thompsona, tylko tak jak sta&#322;, w koszuli, w fura&#380;erce na g&#322;owie i z coltem w kaburze poszed&#322; na czele trzynastu podoficer&oacute;w w kierunku ruin klasztoru. Mijali kikuty drzew oliwnych poszarpanych ogniem i ostro&#380;nie st&#261;pali mi&#281;dzy kamieniami i k&#281;pami czerwonych mak&oacute;w. Trzeba by&#322;o uwa&#380;a&#263; nie tylko na rozk&#322;adaj&#261;ce si&#281; w s&#322;o&#324;cu cia&#322;a poleg&#322;ych, ale i na miny. W ka&#380;dej chwili te&#380; mogli zosta&#263; skoszeni seri&#261; spandaua lub rozsiekani pociskiem mo&#378;dzierzowym. Dobrze ju&#380; poznali metody walki zielonych diab&#322;&oacute;w, kt&oacute;rzy ju&#380; nie raz tutaj wykorzystywali bia&#322;&#261; flag&#281; &ndash; znak kapitulacji, jako fortel do niespodziewanego ataku. Ale tym razem nic takiego nie nast&#261;pi&#322;o.</span>
          </p>
          <p class="c0"><span class="c1">Po kilku godzinach kluczenia, &#347;cie&#380;k&#261; mi&#281;dzy ska&#322;ami, kt&oacute;rych nie mog&#322;a zdoby&#263; ca&#322;a aliancka 15 Grupa Armii, oko&#322;o dziesi&#261;tej u&#322;ani stan&#281;li przed grubymi i poszczerbionymi murami benedykty&#324;skiej kolebki chrze&#347;cija&#324;stwa. Podeszli do najmniej zniszczonego budynku i kiedy podporucznik Gurbiel pchn&#261;&#322; jego drzwi, poczuli straszny fetor. W &#347;rodku le&#380;a&#322;o 16 ci&#281;&#380;ko rannych spadochroniarzy, przy kt&oacute;rych sta&#322;o kilku sanitariuszy. To byli twardzi &#380;o&#322;nierze, ale ich oczy zdradza&#322;y przera&#380;enie, jakie budzi&#322;a w nich &#347;wiadomo&#347;&#263;, &#380;e przyjd&#261; po nich Polacy i b&#281;d&#261; si&#281; m&#347;ci&#263; za ich zbrodnie w Polsce. Ci jednak nie zni&#380;yli si&#281; do poziomu elity Hitlera. Podporucznik Gurbiel przez t&#322;umacza o&#347;wiadczy&#322; kr&oacute;tko: &bdquo;W&#322;os wam z g&#322;owy nie spadnie&rdquo;. Zszed&#322; nast&#281;pnie do piwnicy pod izb&#261; sanitarn&#261; i w&#322;osy stan&#281;&#322;y mu d&#281;ba pod fura&#380;erk&#261;: kazamaty zawalone by&#322;y skrzyniami z poszarpanymi trupami strzelc&oacute;w spadochronowych, a mi&#281;dzy nimi le&#380;a&#322;o jeszcze trzech ci&#281;&#380;ko rannych, kt&oacute;rych pozostawiono tam, by dogorywali.</span>
          </p>
          <p class="c0"><span class="c8">Zwyci&#281;stwo</span><span class="c1">&nbsp;</span></p>
          <p class="c0"><span class="c3">Podporucznik Gurbiel wybieg&#322; na zewn&#261;trz, by zaczerpn&#261;&#263; &#347;wie&#380;ego powietrza i och&#322;on&#261;&#263; ze zgrozy, kt&oacute;ra mog&#322;a poruszy&#263; najbardziej zaprawionego w bojach weterana. Kiedy chodzi&#322; mi&#281;dzy ruinami, nagle zauwa&#380;y&#322;, &#380;e pod g&oacute;r&#281; klasztorn&#261; pnie si&#281; jakich&#347; dw&oacute;ch ludzi. Okazali si&#281; nimi oficer kanadyjski i niemiecki jeniec w roli przewodnika. Kanadyjczyk nie potrafi&#322; ukry&#263; rozczarowania, &#380;e to nie jemu przypad&#322;a palma pierwsze&#324;stwa stani&#281;cia w najs&#322;ynniejszej tej wiosny reducie nieprzyjaciela. Za Kanadyjczykiem na szczyt dotar&#322;y u&#322;a&#324;skie posi&#322;ki &ndash; pluton porucznika Leona Hrynkiewicza. Jeden z jego u&#322;an&oacute;w, J&oacute;zef Bruli&#324;ski wyci&#261;gn&#261;&#322; zza bluzy napr&#281;dce sporz&#261;dzony z czerwonej i granatowej chusty oraz z banda&#380;y proporzec 12 Pu&#322;ku U&#322;an&oacute;w Podolskich i zawiesi&#322; wysoko na &#380;erdzi. I tak amarantowo-bia&#322;o-granatowe barwy noszone przez u&#322;an&oacute;w podolskich od czas&oacute;w napoleo&#324;skich sta&#322;y si&#281; pierwszym znakiem obwieszczaj&#261;cym &#347;wiatu polskie zwyci&#281;stwo na Monte Cassino. Nied&#322;ugo zast&#261;piono go bia&#322;o-czerwon&#261; flag&#261;, obok kt&oacute;rej na rozkaz </span><span
                  class="c3"><a class="c5"
                                href="https://www.google.com/url?q=http://www.polska-zbrojna.pl/home/articleshow/24671?t%3DPretorianie-gen-Andersa-czyli-gotowi-na-wszystko-&amp;sa=D&amp;source=editors&amp;ust=1619038296808000&amp;usg=AOvVaw394gh1PoiczcfCuUGS73az">gen. W&#322;adys&#322;awa Andersa</a></span><span
                  class="c1">&nbsp;powieszono i flag&#281; brytyjsk&#261;. O godzinie 11.15 podporucznik Tadeusz Drabczy&#324;ski wypu&#347;ci&#322; go&#322;&#281;bia pocztowego, kt&oacute;ry przyni&oacute;s&#322; do sztabu 2 Korpusu Polskiego meldunek o zaj&#281;ciu wzg&oacute;rza klasztornego. Na kartce napisana by&#322;a tylko jedna wielka litera &bdquo;V&rdquo; &ndash; jak victory. W samo po&#322;udnie z ruin klasztoru pop&#322;yn&#281;&#322;a melodia hejna&#322;u mariackiego, odegrana przez plutonowego Emila Czecha. Tak polscy &#380;o&#322;nierze uczcili zaj&#281;cie kluczowej pozycji w linii Gustawa.</span>
          </p>
          <p class="c0"><span class="c1">Cho&#263; na okolicznych wzg&oacute;rzach Polacy, wci&#261;&#380; walczyli z wycofuj&#261;cymi si&#281; niemieckimi spadochroniarzami, na r&#281;ce gen. Andersa posypa&#322;y si&#281; gratulacje. Podkre&#347;lano w nich, &#380;e niez&#322;omno&#347;&#263; jego Korpusu da&#322;a wygran&#261; w ca&#322;ej bitwie o Monte Cassino. Komplementowano polskich &#380;o&#322;nierzy, przydaj&#261;c im pierwsze miejsce po&#347;r&oacute;d wszystkich alianckich narod&oacute;w.</span>
          </p>
          <p class="c0" id="h.gjdgxs"><span class="c3">Zgodnie z przewidywaniami gen. Andersa, do lamusa posz&#322;a sowiecka propaganda o tym, &#380;e Polacy nie chc&#261; bi&#263; si&#281; z Niemcami. Sprawa polska znowu znalaz&#322;a si&#281; na pierwszych stronach gazet. Wydawa&#322;o si&#281;, &#380;e to prze&#322;omowe zwyci&#281;stwo cho&#263; okupione wielkimi stratami, wreszcie obudzi sumienie w&#322;odarzy zachodniego &#347;wiata i powstrzymaj&#261; oni zaborcze zap&#281;dy Stalina wobec Polski. Niestety, cho&#263; 2 Korpus Polski jeszcze nie raz triumfowa&#322; w Italii, A</span><span
                  class="c3"><a class="c5"
                                href="https://www.google.com/url?q=http://www.polska-zbrojna.pl/home/articleshow/24671?t%3DPretorianie-gen-Andersa-czyli-gotowi-na-wszystko-&amp;sa=D&amp;source=editors&amp;ust=1619038296809000&amp;usg=AOvVaw14YQ-bWaf41-nlqlJO-E-J">merykanie i Brytyjczycy nie zamierzali dra&#380;ni&#263; Stalina</a></span><span
                  class="c1">&nbsp;&ndash; dla nich wa&#380;niejszy by&#322; potencja&#322; Armii Czerwonej, bez kt&oacute;rego nie wyobra&#380;ali sobie wygrania tej wojny. Lecz nic ju&#380; nie mog&#322;o wymaza&#263; pami&#281;ci o polskiej bitwie pod Monte Cassino &ndash; wesz&#322;a ona do narodowego panteonu:</span>
          </p>
          <p class="c0"><span
                  class="c1">Run&#281;li przez ogie&#324; strace&#324;cy,<br>Niejeden z nich dosta&#322; i pad&#322;,<br>Jak ci z Somosierry szale&#324;cy,<br>Jak ci spod Rac&#322;awic sprzed lat.<br>Run&#281;li impetem szalonym<br>I doszli. I uda&#322; si&#281; szturm.<br>I sztandar sw&oacute;j bia&#322;o-czerwony<br>Zatkn&#281;li na gruzach w&#347;r&oacute;d chmur.</span>
          </p>
          <p class="c0"><span class="c1">&nbsp;</span></p>
          <p class="c0"><span class="c8">Bibliografia</span><span class="c1">&nbsp;</span></p>
          <p class="c0"><span class="c3 c4">W.F. &#321;uczy&#324;ski, &bdquo;Moje cztery kampanie&rdquo;, Krak&oacute;w &ndash; Warszawa 2014</span><span
                  class="c3">&nbsp;<br></span><span class="c3 c4">Z. Wawer, &bdquo;Monte Cassino. Walki 2 Korpusu Polskiego&rdquo;, Warszawa 2009</span><span
                  class="c3">&nbsp;<br></span><span class="c3 c4">M. Wa&#324;kowicz, &bdquo;Bitwa o Monte Cassino&rdquo;, Pozna&#324; 2009</span><span
                  class="c1">&nbsp;</span></p>
          <p class="c6 c10"><span class="c1"></span></p>
        </blockquote>
      </div>
    </div>
