<template>
  <div class="container ">
    <div class="row">
      <div class="col ps-4 pe-1 py-2 text-start">
        <blockquote>
          <p class="c1"><span class="c5 c3">Ewakuacja oznacza&#322;a &#380;ycie</span></p>
          <p class="c1"><span class="c2">Piotr Korczy&#324;ski</span></p>
          <p class="c1 c4"><span class="c3 c5"></span></p>
          <p class="c1" id="h.gjdgxs"><span class="c2">Sytuacja Polskich Si&#322; Zbrojnych w ZSRS &ndash; jak oficjalnie nazwano Armi&#281; genera&#322;a W&#322;adys&#322;awa Andersa &ndash; od pocz&#261;tku by&#322;a trudna. Poprawia&#322;a si&#281; nieco, kiedy Niemcy odnosili kolejne sukcesy na froncie. Gdy zostali odparci od Moskwy, Stalin zdecydowa&#322; si&#281; definitywnie pozby&#263; niewygodnego dla&#324; wojska. </span>
          </p>
          <p class="c1"><span class="c0 c3">Osiemnastoletni &nbsp;Artur Gruber, jeden z tysi&#281;cy Polak&oacute;w uwi&#281;zionych po 17 wrze&#347;nia 1939 roku w sowieckim gu&#322;agu, wspomina&#322;, &#380;e kiedy stan&#261;&#322; przed wojskow&#261; komisj&#261; lekarsk&#261;, wygl&#261;da&#322; jak szkielet odziany w &#322;achmany. &bdquo;Niemi&#322;osiernie brudny szkielet&rdquo; &ndash; doda&#322;. Badaj&#261;cy go m&#322;ody lekarz, w pewnym momencie zapyta&#322; swego starszego koleg&#281;: &bdquo;Dlaczego oni wszyscy maj&#261; takie czerwone kropki jedn&#261; przy drugiej?&rdquo;. </span>
          </p>
          <p class="c1"><span class="c0 c3">Ten lekarz musia&#322; przyby&#263; z Wielkiej Brytanii, bo gdyby by&#322; jednym z zes&#322;a&#324;c&oacute;w czy &#322;agiernik&oacute;w w Rosji, wiedzia&#322;by, &#380;e to &#347;lady po uk&#261;szeniach wszy&hellip; Tak wygl&#261;da&#322;a wi&#281;kszo&#347;&#263; rekrut&oacute;w zg&#322;aszaj&#261;cych si&#281; do punkt&oacute;w rekrutacyjnych Armii genera&#322;a Andersa. Ich sytuacja w obozie wojskowym polepszy&#322;a si&#281; tylko nieznacznie. Nadal cierpieli g&#322;&oacute;d i zimno, gdy&#380; sowieccy sojusznicy nie przysy&#322;ali ani wystarczaj&#261;cych racji &#380;ywno&#347;ciowych, ani zaopatrzenia.</span>
          </p>
          <p class="c1"><span class="c0 c3">Sytuacji nie poprawi&#322;a dyslokacja Armii do Uzbekistanu, Kirgizji i Kazachstanu pod koniec grudnia 1941 roku. Wprawdzie &#380;o&#322;nierze i ich rodziny nie wystawieni byli ju&#380; na mrozy, ale szybko zacz&#281;&#322;y dziesi&#261;tkowa&#263; ich choroby zaka&#378;ne. Znacznie pogorszy&#322;y si&#281; te&#380; warunki zakwaterowania, a przede wszystkim wy&#380;ywienie. A to ostatnie, jak si&#281; okaza&#322;o, mia&#322;o zosta&#263; jeszcze bardziej ograniczone.</span>
          </p>
          <p class="c1"><span class="c2">W matni</span></p>
          <p class="c1"><span class="c0 c3">W czasie spotkania Naczelnego Wodza, genera&#322;a W&#322;adys&#322;awa Sikorskiego z J&oacute;zefem Stalinem na pocz&#261;tku grudnia 1941 roku ustalono, &#380;e ze Zwi&#261;zku Sowieckiego ewakuowanych zostanie 25 tysi&#281;cy polskich &#380;o&#322;nierzy &ndash; lotnik&oacute;w i specjalist&oacute;w r&oacute;&#380;nych broni, kt&oacute;rych brakowa&#322;o Polskim Si&#322;om Zbrojnym na Zachodzie. Do tego jak ewakuowa&#263; tych ludzi do Wielkiej Brytanii i na Bliski Wsch&oacute;d doszed&#322; genera&#322;owi Andersowi kolejny, jeszcze powa&#380;niejszy problem. Mianowicie Sowieci zacz&#281;li naciska&#263;, by wys&#322;a&#322; na front 5 Dywizj&#281; Piechoty. By&#322;a to jedyna w Armii Andersa w miar&#281; uzbrojona dywizja. Reszty jednostek ani Rosjanie, ani Brytyjczycy nie chcieli wyposa&#380;a&#263;, a wszelkie rozmowy na ten temat nie dawa&#322;y &#380;adnego efektu. Polacy po przykrych do&#347;wiadczeniach we Francji w 1940 roku, bronili si&#281; przed podzia&#322;em Armii i wysy&#322;aniem na front jej pojedynczych dywizji. Genera&#322; Anders wi&#281;c, za zgod&#261; genera&#322;a Sikorskiego, zdecydowanie odm&oacute;wi&#322;. </span>
          </p>
          <p class="c1"><span class="c0 c3">&nbsp;Otrzyma&#322; na to odpowied&#378; 6 marca 1942 roku. Szef zaopatrzenia Armii Czerwonej, genera&#322; Andriej Chrulew zawiadomi&#322; o drastycznym zmniejszeniu racji &#380;ywno&#347;ciowych do stanu 26 tysi&#281;cy. To oznacza&#322;o g&#322;&oacute;d dla wielu &#380;o&#322;nierzy i prawie wszystkich cywil&oacute;w, zgromadzonych przy Armii. Genera&#322; Anders natychmiast postanowi&#322; interweniowa&#263; u Stalina, argumentuj&#261;c, &#380;e narusza to porozumienie wynegocjowane przez genera&#322;a Sikorskiego. Stalin przyj&#261;&#322; Andersa na audiencji 18 marca i zgodzi&#322; si&#281; podwy&#380;szy&#263; racj&#281; &#380;ywno&#347;ciowe do 44 tysi&#281;cy. To mia&#322;o wystarczy&#263; dla trzech dywizji i pu&#322;ku zapasowego. Reszta jednostek mia&#322;a by&#263; ewakuowana na Bliski Wsch&oacute;d. </span>
          </p>
          <p class="c1"><span class="c2">Nie patrze&#263; za siebie</span></p>
          <p class="c1"><span class="c0 c3">Kilkadziesi&#261;t minut po zako&#324;czeniu rozmowy ze Stalinem, genera&#322; Anders spotka&#322; si&#281; z genera&#322;em NKWD Gieorgijem Sergiejowiczem &#379;ukowem, pe&#322;nomocnikiem rz&#261;du sowieckiego do spraw formowania wojsk polskich w ZSRS. Ku zdziwieniu polskiego dow&oacute;dcy, &#379;ukow mia&#322; przy sobie komplet dokument&oacute;w do ewakuacji oko&#322;o 30 tysi&#281;cy &#380;o&#322;nierzy i 10 tysi&#281;cy cz&#322;onk&oacute;w ich rodzin, czyli tej nadwy&#380;ki ludzi, kt&oacute;rym Sowieci nie zapewniali wy&#380;ywienia. Jak podkre&#347;la profesor Edward Kospath-Paw&#322;owski, &bdquo;Przemawia to za tez&#261;, &#380;e Stalin d&#261;&#380;y&#322; do ewakuacji polskich oddzia&#322;&oacute;w z ZSRR, a sama ewakuacja mia&#322;a rozpocz&#261;&#263; si&#281; w kilkadziesi&#261;t godzin p&oacute;&#378;niej [od spotkania z Andersem &ndash; PK]&rdquo;. </span>
          </p>
          <p class="c1 c11"><span class="c9">Pierwsze rozkazy o ewakuacji genera&#322; Anders wyda&#322; jeszcze w Moskwie, 19 marca. Ewakuacja mia&#322;a przebiega&#263; przez baz&#281; w Krasnowodsku, kt&oacute;rej dow&oacute;dc&#261; genera&#322; mianowa&#322; podpu&#322;kownika Zygmunta Berlinga. Pierwszy transport odp&#322;yn&#261;&#322; st&#261;d do ira&#324;skiego Pehlewi 24 marca. Oddajmy w tym miejscu g&#322;os wspomnianemu na pocz&#261;tku Arturowi Gruberowi: &bdquo;</span><span
                  class="c0">Na pok&#322;adzie sowieckiego tankowca opuszczali&#347;my</span><span
                  class="c0 c7">&nbsp;</span><span class="c0">bez &#380;alu kraj niespe&#322;nionych nadziei i bardzo smutnych, szarych ludzi.</span><span
                  class="c6">&nbsp;P</span><span class="c0">ozostawili&#347;my po sobie cmentarz. Epidemie tyfusu plamistego i brzusznego oraz krwawej dyzenterii zbiera&#322;y obfite &#380;niwo w&#347;r&oacute;d wycie&#324;czonych Polak&oacute;w. Niekt&oacute;rzy twierdz&#261;, &#380;e spoczywa ich tam wi&#281;cej ni&#380; pod g&oacute;r&#261; Monte Cassino. &nbsp;Drugiego dnia wieczorem ujrzeli&#347;my brzegi Iranu. Port Pehlewi by&#322; sk&#261;po o&#347;wietlony &#347;wiat&#322;em elektrycznym. Po zej&#347;ciu na l&#261;d s&#322;ysz&#261;c polsk&#261; mow&#281; uwijaj&#261;cych si&#281; sanitariuszy przyjmuj&#261;cych chorych z naszego transportu, poczu&#322;em si&#281; jak w Polsce, w&#347;r&oacute;d swoich, bezpiecznie&rdquo;.</span>
          </p>
          <p class="c1 c4"><span class="c0 c3"></span></p>
          <p class="c1"><span class="c0 c3">Ewakuacja ta trwa&#322;a do 5 kwietnia. Do Iranu (wtedy nazywanego Persj&#261;) dotarli w&oacute;wczas &#380;o&#322;nierze 8, 9 i 10 Dywizji Piechoty, brygady pancernej, pu&#322;k&oacute;w u&#322;an&oacute;w i saper&oacute;w, kompani saper&oacute;w kolejowych oraz wi&#281;ksza cz&#281;&#347;&#263; O&#347;rodka Organizacyjnego Armii, nadwy&#380;ki 6 i 7 Dywizji Piechoty. Ponadto do Pehlewi zaokr&#281;towano wszystkich lotnik&oacute;w i marynarzy polskich, jakich zdo&#322;ano w Rosji odnale&#378;&#263; w&#347;r&oacute;d &#380;ywych.</span>
          </p>
          <p class="c1"><span class="c2">Wychodzimy wszyscy</span></p>
          <p class="c1"><span class="c0 c3">Szybko okaza&#322;o si&#281;, &#380;e to nie koniec ewakuacji polskich wojsk ze Zwi&#261;zku Sowieckiego. 14 maja Wiaczes&#322;aw Mo&#322;otow w nocie do polskiego ambasadora w Moskwie, Stanis&#322;awa Kota, podkre&#347;li&#322;, &#380;e nie widzi mo&#380;liwo&#347;ci prowadzenia dalszego zaci&#261;gu do polskiej Armii, skoro ma ona by&#263; wys&#322;ana poza granice ZSRS. To stwierdzenie by&#322;o pok&#322;osiem wizyty Mo&#322;otowa w Londynie, podczas kt&oacute;rej &ndash; bez wiedzy Polak&oacute;w &ndash; Brytyjczycy zaproponowali, by przesun&#261;&#263; pozosta&#322;e w Rosji wojska polskie na Bliski Wsch&oacute;d. Zwolennikiem takiego rozwi&#261;zania by&#322; r&oacute;wnie&#380; genera&#322; Anders, kt&oacute;ry s&#322;a&#322; do sztabu Naczelnego Wodza raporty o coraz gorszej sytuacji aprowizacyjnej swoich oddzia&#322;&oacute;w. Natomiast genera&#322; Sikorski nadal wi&#261;za&#322; swe nadzieje z obecno&#347;ci&#261; polskich &#380;o&#322;nierzy na froncie wschodnim. Jak mia&#322;o si&#281; okaza&#263;, decyzj&#281; o losie polskiej Armii w ZSRS, podj&#281;to bez wiedzy obu genera&#322;&oacute;w i niezale&#380;nie od ich opinii na ten temat. </span>
          </p>
          <p class="c1"><span class="c0 c3">Pod koniec czerwca 1942 roku Mo&#322;otow zadepeszowa&#322; do Londynu, &#380;e rz&#261;d sowiecki zgadza si&#281; na oddanie trzech polskich dywizji pozostaj&#261;cych na terytorium sowieckim. Churchill podzi&#281;kowa&#322; Stalinowi za polskich &#380;o&#322;nierzy, kt&oacute;rych &bdquo;tak uprzejmie ofiarowa&#322;&rdquo;. Brytyjski premier mia&#322; si&#281; z czego cieszy&#263;, gdy&#380; Polacy mogli zapewni&#263; mu os&#322;on&#281; ira&#324;skich i irackich rafinerii i ruroci&#261;g&oacute;w naftowych, kt&oacute;re by&#322;y strategiczne dla prowadzonych dzia&#322;a&#324; na froncie afryka&#324;skim. </span>
          </p>
          <p class="c1"><span class="c0 c3">O tych decyzjach genera&#322; Anders dowiedzia&#322; si&#281; z prawie miesi&#281;cznym op&oacute;&#378;nieniem, a 26 lipca otrzyma&#322; oficjalne pismo, w kt&oacute;rym podkre&#347;lono, &#380;e &bdquo;Rz&#261;d ZSRR zgadza si&#281; uczyni&#263; zado&#347;&#263; staraniom Dow&oacute;dcy Armii Polskiej w ZSRR [&hellip;] o ewakuacj&#281; oddzia&#322;&oacute;w polskich z ZSRR na teren Bliskiego Wschodu i nie ma zamiaru stawiania jakichkolwiek przeszk&oacute;d w natychmiastowym urzeczywistnieniu ewakuacji&rdquo;. 31 lipca polsko-sowiecka komisja wojskowa ustali&#322;a, &#380;e w ci&#261;gu sierpnia z Krasnowodzka do Pehlewi wyjedzie oko&#322;o 70 tysi&#281;cy wojskowych i cywil&oacute;w. W protokole ewakuacyjnym podpisanym przez genera&#322;a Andersa znalaz&#322;o si&#281; mi&#281;dzy innymi zdanie, &#380;e &bdquo;Rz&#261;d Polski nie uwa&#380;a za mo&#380;liwe u&#380;ycie na froncie radziecko-niemieckim oddzia&#322;&oacute;w formowanych w ZSRR&rdquo;. To sformu&#322;owanie pos&#322;u&#380;y&#322;o Sowietom do faktycznego wstrzymania dalszego naboru do polskiej Armii. W obu akcjach ewakuacyjnych na Bliski Wsch&oacute;d ewakuowano 116&nbsp;543 ludzi, w tym 78&nbsp;631 wojskowych. Dla ludzi, kt&oacute;rzy w ten spos&oacute;b mogli opu&#347;ci&#263; &bdquo;nieludzk&#261; ziemi&#281;&rdquo; genera&#322; Anders sta&#322; si&#281; m&#281;&#380;em opatrzno&#347;ciowym i jak celnie podkre&#347;li&#322; profesor Czes&#322;aw Brzoza: &bdquo;&hellip; swego dow&oacute;dc&#281; &#380;o&#322;nierze traktowali jak Moj&#380;esza, kt&oacute;ry wyprowadzi&#322; ich z &laquo;domu niewoli&raquo;&rdquo;. </span>
          </p>
          <p class="c1"><span class="c0 c3">Bibliografia</span></p>
          <p class="c1"><span class="c0 c8">Czes&#322;aw Brzoza, &bdquo;Polska w czasach niepodleg&#322;o&#347;ci i drugiej wojny &#347;wiatowej (1918&ndash;1945)&rdquo;, Krak&oacute;w 2001</span>
          </p>
          <p class="c1"><span class="c0 c8">Zbigniew Dunin-Wilczy&#324;ski, &bdquo;Wojsko Polskie w Iraku 1942&ndash;1943&rdquo;, Warszawa 1993</span>
          </p>
          <p class="c1"><span class="c0 c8">Artur Gruber, &bdquo;W imi&#281; Bo&#380;e&rdquo;, maszynopis w zbiorach autora</span>
          </p>
          <p class="c1"><span class="c0 c8">Edward Kospath-Paw&#322;owski, &bdquo;Wojsko Polskie na Wschodzie 1943&ndash;1945&rdquo;, Pruszk&oacute;w 1997</span>
          </p>
          <p class="c1"><span class="c0 c3">&nbsp; &nbsp; &nbsp;</span></p>
          <p class="c1"><span class="c0 c3">&nbsp; &nbsp; </span></p>

        </blockquote>
      </div>
    </div>
  </div>
