<template>
    <div :class="'arrow-directions position-absolute '+arrows[arrowPosition]">
        <a v-smooth-scroll :href="this.componentLink" @click="setActiveLink()">
            <div class="chevron" v-for="index in 3" :key="index"></div>
        </a>
    </div>
</template>
<script>
const ARROWS = {
    top: "end-50",
    bottom: "custom-arrow-bottom",
    right: "custom-arrow-right",
    left: "custom-arrow-left"
};
export default {
    data() {
        return {
            arrows: ARROWS,
        }
    },
    props: [
        'componentLink',
        'arrowPosition'
    ],
    methods:
        {
            setActiveLink() {
                this.$store.commit('setActiveLink', this.componentLink);
            },
        }
}
</script>
<style lang="scss">
$base: 0.6rem;

.custom-arrow-right {
  bottom: auto !important;
  transform: rotate(270deg);
  right: 2rem;
}

.custom-arrow-bottom {
  left: calc(50% - 0px);
}

.custom-arrow-left {
  bottom: auto !important;
  transform: rotate(90deg);
  left: 2rem;
}

.arrow-directions {
  bottom: 0;
  border-radius: 50px;
  padding: 10px;
}

.arrow-directions a {
  height: 5rem;
  width: 2rem;
  display: block;
}

.chevron {
  position: absolute;
  width: $base * 3.5;
  height: $base * 0.8;
  opacity: 0;
  transform: scale(0.3);
  animation: move-chevron 3s ease-out infinite;
}

.chevron:first-child {
  animation: move-chevron 3s ease-out 1s infinite;
}

.chevron:nth-child(2) {
  animation: move-chevron 3s ease-out 2s infinite;
}

.chevron::before,
.chevron::after {
  content: '';
  position: absolute;
  top: 0;
  height: 100%;
  width: 50%;
  background: #ffa500;
}

.chevron::before {
  left: 0;
  transform: skewY(30deg);
}

.chevron::after {
  right: 0;
  width: 50%;
  transform: skewY(-30deg);
}

@keyframes move-chevron {
  25% {
    opacity: 1;
  }
  33.3% {
    opacity: 1;
    transform: translateY($base * 3.8);
  }
  66.6% {
    opacity: 1;
    transform: translateY($base * 5.2);
  }
  100% {
    opacity: 0;
    transform: translateY($base * 8) scale(0.5);
  }
}
</style>