<template>
  <div class="container ">
    <div class="row">
      <div class="col ps-4 pe-1 py-2">
        <blockquote>
          <p class="c6"><span class="c1 c8">A wi&#281;c wojna...</span></p>
          <p class="c3"><span class="c8 c1">&#321;ukasz Zalesi&#324;ski</span></p>
          <p class="c3" id="h.gjdgxs"><span class="c1">&nbsp;&bdquo;B&#261;d&#378;cie bezlito&#347;ni, b&#261;d&#378;cie brutalni&rdquo; &ndash; apelowa&#322; do swoich dow&oacute;dc&oacute;w Adolf Hitler. 1 wrze&#347;nia 1939 roku o &#347;wicie niemiecka machina wojenna ruszy&#322;a na Polsk&#281; rozpoczynaj&#261;c wojn&#281;, kt&oacute;ra otworzy&#322;a w dziejach &#347;wiata zupe&#322;nie now&#261; er&#281;. Pierwsze starcie Polacy przegrali, ale zdo&#322;ali zada&#263; przeciwnikowi znacz&#261;ce straty.</span><span
                  class="c2"><br>Punktualnie o 6:30 program Polskiego Radia zosta&#322; przerwany, za&#347; w eter pop&#322;yn&#261;&#322; komunikat odczytany przez spikera J&oacute;zefa Ma&#322;gorzewskiego. &bdquo;A wi&#281;c wojna! Z dniem dzisiejszym wszelkie sprawy i zagadnienia schodz&#261; na plan dalszy. Ca&#322;e nasze &#380;ycie publiczne i prywatne przestawiamy na specjalne tory [&hellip;]. Ca&#322;y wysi&#322;ek narodu musi i&#347;&#263; w jednym kierunku. Wszyscy jeste&#347;my &#380;o&#322;nierzami. Musimy my&#347;le&#263; tylko o jednym &ndash; walka a&#380; do zwyci&#281;stwa!&rdquo;. By&#322; pi&#261;tek, 1 wrze&#347;nia 1939 roku.<br>Niespe&#322;na dwie godziny wcze&#347;niej na p&oacute;&#322;wysep Westerplatte, gdzie mie&#347;ci&#322;a si&#281; polska sk&#322;adnica wojskowa, spad&#322;y pierwsze pociski wystrzelone z pancernika &bdquo;Schleswig-Holstein&rdquo;</span>
            <span class="c2">, samoloty Luftwaffe zd&#261;&#380;y&#322;y ju&#380; obr&oacute;ci&#263; w perzyn&#281; przygraniczne miasto Wielu&#324;, a pancerne zagony Wehrmachtu z impetem uderzy&#322;y na kilka polskich armii. Tego pogodnego ranka historia &#347;wiata wskoczy&#322;a na zupe&#322;nie nowe tory. Sta&#322;o si&#281; to, czego od dobrych kilku miesi&#281;cy spodziewali si&#281; chyba wszyscy.<br><br></span><span
                    class="c1">Pretekst z g&#322;owy</span><span class="c2"><br>Adolf Hitler chcia&#322; wojny. Kiedy sze&#347;&#263; lat wcze&#347;niej obejmowa&#322; w&#322;adz&#281;, postawi&#322; sobie jasny cel: przenicowa&#263; </span><span
                    class="c2"><a class="c0"
                                  href="https://www.google.com/url?q=http://polska-zbrojna.pl/home/articleshow/28656?t%3DW-Wersalu-zapewniono-pokoj-na-20-lat&amp;sa=D&amp;source=editors&amp;ust=1616354062484000&amp;usg=AOvVaw2vexAkihpQBs0EyI8E48X2">&#322;ad wersalski</a></span><span
                    class="c2">, odbudowa&#263; pot&#281;g&#281; Niemiec, na nowo rozrysowa&#263; map&#281; Europy &ndash; bez &#379;yd&oacute;w i ze &#347;ladow&#261; liczb&#261; S&#322;owian obsadzonych w roli niewolnik&oacute;w. Do celu d&#261;&#380;y&#322; ma&#322;ymi krokami, testuj&#261;c cierpliwo&#347;&#263; i determinacj&#281; zachodnich aliant&oacute;w. A ci pozwalali mu w&#322;a&#347;ciwie na wszystko. Wbrew ustaleniom z Wersalu, niemiecka armia puch&#322;a w oczach, obrastaj&#261;c w nowoczesne wojska pancerne, lotnictwo i marynark&#281; wojenn&#261;. W 1936 roku Hitler zaj&#261;&#322; zdemilitaryzowan&#261; Nadreni&#281;. Dwa lata p&oacute;&#378;niej wcieli&#322; do Rzeszy Austri&#281;. Drog&#261; dyplomatycznych gier i militarnych nacisk&oacute;w podporz&#261;dkowa&#322; te&#380; sobie Czechos&#322;owacj&#281;. Kolejnym celem sta&#322;a si&#281; Polska.<br>Jeszcze w pa&#378;dzierniku 1938 roku minister spraw zagranicznych Rzeszy Joachim von Ribbentrop z&#322;o&#380;y&#322; polskiemu ambasadorowi J&oacute;zefowi Lipskiemu propozycj&#281; dotycz&#261;c&#261; m.in. w&#322;&#261;czenia do Niemiec Wolnego Miasta Gda&#324;ska oraz budow&#281; eksterytorialnych autostrady i linii kolejowej, kt&oacute;re przecina&#322;yby tzw. Korytarz Polski. W zamian zaoferowa&#322; m.in. przed&#322;u&#380;enie o 25 lat paktu o nieagresji oraz gwarancj&#281; zbytu w Gda&#324;sku polskich towar&oacute;w. Wiosn&#261; 1939 roku memorandum do rz&#261;du w Warszawie wystosowa&#322; sam Hitler. Polacy byli ju&#380; jednak pewni: Rzesza prowadzi gr&#281;, kt&oacute;ra w najlepszym razie ma na celu zwasalizowanie s&#261;siada. Powiedzieli: &bdquo;nie&rdquo;. &ndash; Polska od Ba&#322;tyku odepchn&#261;&#263; si&#281; nie da! &ndash; grzmia&#322; z sejmowym expose szef polskiego MSZ J&oacute;zef Beck. Przygotowania do wojny ruszy&#322;y pe&#322;n&#261; par&#261;.<br>Plan niemieckiej inwazji otrzyma&#322; kryptonim &bdquo;Fall Weiss&rdquo;. Wehrmacht mia&#322; uderzy&#263; z p&oacute;&#322;nocy i zachodu, a tak&#380;e kontrolowanych przez Rzesz&#281; terytori&oacute;w Czech i S&#322;owacji. &bdquo;Zniszczenie Polski jest naszym pierwszym zadaniem. Celem musi by&#263; nie dotarcie do jakiej&#347; oznaczonej linii, lecz zniszczenie &#380;ywej si&#322;y. B&#261;d&#378;cie bezlito&#347;ni, b&#261;d&#378;cie brutalni&rdquo; &ndash; apelowa&#322; Hitler do swoich dow&oacute;dc&oacute;w podczas narady w ostatnich dniach sierpnia. </span><span
                    class="c2"><a class="c0"
                                  href="https://www.google.com/url?q=http://polska-zbrojna.pl/home/articleshow/29048?t%3DBrunatno-czerwona-zmowa&amp;sa=D&amp;source=editors&amp;ust=1616354062485000&amp;usg=AOvVaw1Cx6SECgUBuYENDUqwXq9B">Po cichu uk&#322;ada&#322; si&#281; ze Zwi&#261;zkiem Sowieckim</a></span><span
                    class="c2">, kt&oacute;ry mia&#322; mu udzieli&#263; wojskowego poparcia.<br>Polscy wojskowi z kolei od marca gor&#261;czkowo pracowali nad &bdquo;Planem Operacyjnym Zach&oacute;d&rdquo;. Dla nich perspektywa wojny z Niemcami by&#322;a czym&#347; nowym. Od lat za daleko wi&#281;ksze zagro&#380;enie uznawali Armi&#281; Czerwon&#261;. Teraz plani&#347;ci zak&#322;adali, &#380;e pierwszy etap walk rozegra si&#281; wzd&#322;u&#380; granic. Je&#347;li agresora nie uda si&#281; zatrzyma&#263;, Polacy b&#281;d&#261; si&#281; broni&#263; na linii Wis&#322;y. Kluczow&#261; kwesti&#261; w tej uk&#322;adance by&#322;a jednak postawa sojusznik&oacute;w. Polskie w&#322;adze liczy&#322;y, &#380;e kilka dni po niemieckiej agresji, do walki w&#322;&#261;cz&#261; si&#281; Francji oraz Wielka Brytania, a wzi&#281;ty w kleszcze Wehrmacht skapituluje. Decydenci nie wiedzieli w&oacute;wczas, &#380;e do uderzenia na Polsk&#281; szykuje si&#281; te&#380; Armia Czerwona...<br>Latem Niemcy rozpocz&#281;li wzmo&#380;one dzia&#322;ania dywersyjne. Atakowali posterunki graniczne, zaaran&#380;owali </span><span
                    class="c2"><a class="c0"
                                  href="https://www.google.com/url?q=http://polska-zbrojna.pl/home/articleshow/26191?t%3DDywersja-tajna-bron-Hitlera&amp;sa=D&amp;source=editors&amp;ust=1616354062485000&amp;usg=AOvVaw0IWwoTjHKg8mjwk_TjGkYf">zamach bombowy na dworcu w Tarnowie</a></span><span
                    class="c2">, pr&oacute;bowali zaj&#261;&#263; </span><span class="c2"><a class="c0"
                                                                                             href="https://www.google.com/url?q=http://polska-zbrojna.pl/home/articleshow/29041?t%3DOperacja-dywersyjna-przed-wojna&amp;sa=D&amp;source=editors&amp;ust=1616354062486000&amp;usg=AOvVaw3FusaJPTJDQzmv71d5LgV3">strategiczny tunel na Prze&#322;&#281;czy Jab&#322;onkowskiej</a></span><span
                    class="c2">. Kulminacj&#261; ca&#322;ej serii incydent&oacute;w sta&#322;a si&#281; </span><span
                    class="c2"><a
                    class="c0"
                    href="https://www.google.com/url?q=http://polska-zbrojna.pl/home/articleshow/20355?t%3DHitlerowska-prowokacja-gliwicka&amp;sa=D&amp;source=editors&amp;ust=1616354062486000&amp;usg=AOvVaw3vX6qbx_LvzV3ACuHaQGaY">prowokacja gliwicka</a></span><span
                    class="c2">, czyli rajd uzbrojonego komanda na radiostacj&#281;, kt&oacute;ra znajdowa&#322;a si&#281;... po niemieckiej stronie granicy. Nazi&#347;ci odpowiedzialno&#347;ci&#261; za wypad chcieli obarczy&#263; Polak&oacute;w. Mimo &#380;e akcja zosta&#322;a przeprowadzona nad wyraz nieudolnie, kilkadziesi&#261;t godzin p&oacute;&#378;niej Hitler wymieni&#322; j&#261; jako &bdquo;casus belli&rdquo;. Zgodnie zreszt&#261; ze swoim credo, kt&oacute;re na przywo&#322;anej ju&#380; naradzie wy&#322;o&#380;y&#322; swoim dow&oacute;dcom: &bdquo;Dla cel&oacute;w propagandy podam jak&#261;&#347; przyczyn&#281; wybuchu wojny, mniejsza z tym, czy b&#281;dzie ona wiarygodna, czy nie. Zwyci&#281;zcy nikt nie pyta, czy powiedzia&#322; prawd&#281;, czy te&#380; nie. W sprawach zwi&#261;zanych z rozpocz&#281;ciem i prowadzeniem wojny nie decyduje prawo, lecz zwyci&#281;stwo&rdquo;.<br><br></span><span
                    class="c1">Pi&#281;&#263; tygodni walki</span><span class="c2"><br>25 sierpnia do portu w Gda&#324;sku wp&#322;yn&#261;&#322; pancernik &bdquo;Schleswig-Holstein&rdquo;. Oficjalnie jego za&#322;oga mia&#322;a wzi&#261;&#263; udzia&#322; w uroczysto&#347;ciach upami&#281;tnienia marynarzy z kr&#261;&#380;ownika &bdquo;Magdeburg&rdquo;, kt&oacute;ry zaton&#261;&#322; podczas I wojny &#347;wiatowej. Faktycznie okr&#281;t sposobi&#322; si&#281; do ataku na polsk&#261; sk&#322;adnic&#281; na Westerplatte. Zanim jednak pad&#322;y pierwsze salwy, Niemcy zgodnie z dyplomatycznym ceremonia&#322;em zaprosili na pok&#322;ad lokalnych decydent&oacute;w. &bdquo;Kieliszek szampana przy akompaniamencie drwi&#261;cych u&#347;mieszk&oacute;w smarkatych oficerk&oacute;w. To by&#322;a najtrudniejsza chwila w moim &#380;yciu&rdquo; &ndash; zanotowa&#322; w dzienniku Marian Chodacki, sekretarz generalny RP w Gda&#324;sku. 1 wrze&#347;nia o 4:45 huk okr&#281;towych dzia&#322; przerwa&#322; cisz&#281; poranka. Wi&#281;kszo&#347;&#263; historyk&oacute;w utrzymuje, &#380;e w tym w&#322;a&#347;nie momencie rozpocz&#281;&#322;a si&#281; II wojna &#347;wiatowa.<br>Do walki przeciwko wschodniemu s&#261;siadowi Niemcy rzucili w sumie 1,8 miliona &#380;o&#322;nierzy, 2,8 tysi&#261;ca czo&#322;g&oacute;w i 1,3 tysi&#261;ca samolot&oacute;w. Polska mog&#322;a im przeciwstawi&#263; niespe&#322;na milion &#380;o&#322;nierzy, 880 czo&#322;g&oacute;w i 400 samolot&oacute;w. Strategiczne po&#322;o&#380;enie Rzeczpospolitej by&#322;o przy tym skrajnie niekorzystne. Granice z terytoriami kontrolowanymi przez Rzesz&#281; rozci&#261;ga&#322;y si&#281; na blisko trzy tysi&#261;ce kilometr&oacute;w, w dodatku w wi&#281;kszo&#347;ci miejsc brakowa&#322;o naturalnych przeszk&oacute;d i fortyfikacji. Mobilne i silnie uzbrojone oddzia&#322;y Wehrmachtu w ci&#261;gu trzech pierwszych dni wygra&#322;y wojn&#281; graniczn&#261;, prze&#322;amuj&#261;c obron&#281; przeciwnika i spychaj&#261;c jego armie w g&#322;&#261;b kraju.<br>Nadziej&#281; w serca Polak&oacute;w wla&#322;a decyzja rz&#261;d&oacute;w Francji i Wielkiej Brytanii. 3 wrze&#347;nia, zgodnie z sojuszniczymi zobowi&#261;zaniami, wypowiedzia&#322;y one Niemcom wojn&#281;. </span><span
                    class="c2"><a class="c0"
                                  href="https://www.google.com/url?q=http://polska-zbrojna.pl/home/articleshow/29123?t%3DDziwna-wojna-czyli-jak-alianci-nie-rozprawili-sie-z-Hitlerem&amp;sa=D&amp;source=editors&amp;ust=1616354062487000&amp;usg=AOvVaw0_ayFjwKrWglQaYD3Rj8KU">Do spodziewanej ofensywy na froncie zachodnim jednak nie dosz&#322;o</a></span><span
                    class="c2">. Pow&oacute;d? Alianci uznali, &#380;e nie s&#261; jeszcze gotowi, by stawi&#263; czo&#322;a nazistowskiej machinie wojennej. Nie wszyscy ich dow&oacute;dcy podzielali jednak t&#281; opini&#281;. &bdquo;Mo&#380;na by&#322;o wej&#347;&#263; jak w mas&#322;o w pozycje niemieckie. Mo&#380;na by&#322;o rozstrzygn&#261;&#263; wojn&#281; w 1939 roku. Wystarczy&#322;o troch&#281; zdecydowania i charakteru &ndash; ubolewa&#322; po latach francuski marsza&#322;ek Alphonse Juin. &ndash; Co za ha&#324;ba, co za wstyd, co za g&#322;upota zarazem. To idealna sytuacja, wr&#281;cz modelowa, w kt&oacute;rej mo&#380;na by&#322;o pobi&#263; przeciwnika, wychodz&#261;c na jego ty&#322;y&rdquo;.<br>Tymczasem mimo rosn&#261;cej przewagi przeciwnika, Polacy bili si&#281; dzielnie. W nocy z 9 na 10 wrze&#347;nia zdo&#322;ali nawet wyprowadzi&#263; skuteczne uderzenie. Dowodzone przez gen. Tadeusza Kutrzeb&#281; Armie &bdquo;Pozna&#324;&rdquo; i &bdquo;Pomorze&rdquo; wyprowadzi&#322;y nieoczekiwany cios w skrzyd&#322;o 8 Armii gen. Johannesa Blaskowitza. </span><span
                    class="c2"><a class="c0"
                                  href="https://www.google.com/url?q=http://polska-zbrojna.pl/home/articleshow/20443?t%3D-Polsce-sie-poswiecam-general-znad-Bzury&amp;sa=D&amp;source=editors&amp;ust=1616354062487000&amp;usg=AOvVaw3ie48fbkrXonrG1CKiBK-7">Krwawa bitwa nad Bzur&#261;</a></span><span
                    class="c2">&nbsp;na kilkadziesi&#261;t godzin wyhamowa&#322;a niemieckie natarcie na Warszaw&#281;. Polskie nadzieje ostatecznie jednak pogrzebane zosta&#322;y 17 wrze&#347;nia, kiedy to </span><span
                    class="c2"><a class="c0"
                                  href="https://www.google.com/url?q=http://polska-zbrojna.pl/home/articleshow/29247?t%3DStalin-czekal-i-kalkulowal&amp;sa=D&amp;source=editors&amp;ust=1616354062487000&amp;usg=AOvVaw1_PaJq8Ks6ocCg_SJu4kdG">ze wschodu zaatakowa&#322;a Armia Czerwona</a></span><span
                    class="c2">. Pod koniec wrze&#347;nia pad&#322;a Warszawa, 2 pa&#378;dziernika </span><span
                    class="c2"><a
                    class="c0"
                    href="https://www.google.com/url?q=http://polska-zbrojna.pl/home/articleshow/29371?t%3DOstatni-polski-bastion&amp;sa=D&amp;source=editors&amp;ust=1616354062488000&amp;usg=AOvVaw0pWMy_righGIm7EgQE8Bmx">skapitulowa&#322; Rejon Umocniony Hel</a></span><span
                    class="c2">, a cztery dni p&oacute;&#378;niej </span><span class="c2"><a class="c0"
                                                                                             href="https://www.google.com/url?q=http://polska-zbrojna.pl/home/articleshow/29407?t%3DOstatni-boj-wojny-obronnej&amp;sa=D&amp;source=editors&amp;ust=1616354062488000&amp;usg=AOvVaw1ZolfGkb6FI3CXpRvz9obk">Samodzielna Grupa Operacyjna Polesie</a></span><span
                    class="c2">&nbsp;dowodzona przez gen. Franciszka Kleeberga. Wojna obronna zosta&#322;a przegrana, a Polska znikn&#281;&#322;a z mapy Europy.<br><br></span><span
                    class="c1">To zaledwie pocz&#261;tek...</span><span class="c2"><br>Straty by&#322;y ogromne. Wiele miast zosta&#322;o obr&oacute;conych w gruzy, liczne wioski puszczone z dymem. Niemcy oraz Sowieci pokazali, &#380;e dla osi&#261;gni&#281;cia militarnych oraz ideologicznych cel&oacute;w, </span><span
                    class="c2"><a class="c0"
                                  href="https://www.google.com/url?q=http://polska-zbrojna.pl/home/articleshow/29096?t%3DWehrmacht-sial-terror-i-strach&amp;sa=D&amp;source=editors&amp;ust=1616354062488000&amp;usg=AOvVaw1Vk5ubSwRlasHpyIukAM4h">gotowi s&#261; &#322;ama&#263; wszelkie zasady prowadzenia wojny</a></span><span
                    class="c2">. Celem atak&oacute;w regularnie padali cywile. Wystarczy przywo&#322;a&#263; Wielu&#324; &ndash; w chwili nalotu w miasteczku nie by&#322;o ani jednego &#380;o&#322;nierza, polskie oddzia&#322;y stacjonowa&#322;y kilkadziesi&#261;t kilometr&oacute;w dalej. Jeden ze &#347;wiadk&oacute;w nalotu wspomina&#322;: &bdquo;Zniszczenia by&#322;y ogromne (&hellip;). Obok szpitala na ga&#322;&#281;ziach drzewa widzia&#322;em zwisaj&#261;ce zw&#322;oki. Na ulicach le&#380;a&#322;o sporo zabitych i rannych (&hellip;). Pop&#322;och panowa&#322; nie do opisania. Ludzie uciekali z miasta, wielu biega&#322;o tylko w bieli&#378;nie. Zaskoczenie by&#322;o tak wielkie, &#380;e niekt&oacute;rzy odchodzili od zmys&#322;&oacute;w&rdquo;. W walkach zgin&#281;&#322;o 66 tysi&#281;cy polskich &#380;o&#322;nierzy, blisko 700 tysi&#281;cy dosta&#322;o si&#281; do niewoli.<br>Ale Polacy potrafili te&#380; zada&#263; ci&#281;&#380;kie straty napastnikom, zw&#322;aszcza Niemcom. Planowany przez hitlerowskich dow&oacute;dc&oacute;w Blitzkrieg przerodzi&#322; si&#281; w ci&#281;&#380;kie, trwaj&#261;ce przesz&#322;o miesi&#261;c walki. Podczas kampanii wrze&#347;niowej Wehrmacht straci&#322; przesz&#322;o 17 tysi&#281;cy &#380;o&#322;nierzy, 217 czo&#322;g&oacute;w, ponad dziesi&#281;&#263; tysi&#281;cy r&oacute;&#380;nego typu pojazd&oacute;w i ogromne zapasy amunicji. W efekcie musieli przesun&#261;&#263; inwazj&#281; na zachodni&#261; Europ&#281;. Nie&#380;yj&#261;cy ju&#380; prof. Pawe&#322; Wieczorkiewicz zauwa&#380;a&#322;: &bdquo;Hitler, oceniaj&#261;c wnioski p&#322;yn&#261;ce z kampanii, stwierdzi&#322;, &#380;e &laquo;gdyby Polska mia&#322;a bro&#324; przeciwpancern&#261;, zwyci&#281;ski poch&oacute;d nie by&#322;by mo&#380;liwy&raquo;. Istotnie, kliny czo&#322;gowe, a tak&#380;e lotnictwo, odegra&#322;y w niej rol&#281; decyduj&#261;c&#261;. O druzgoc&#261;cej przewadze materia&#322;owej Wehrmachtu (&hellip;) stanowi&#322;a nie tyle jako&#347;&#263; posiadanego sprz&#281;tu (&hellip;), co jego ilo&#347;&#263; i doktryna zmasowanego u&#380;ycia&rdquo;. W ocenie historyka, podczas walk obronnych, wbrew kolportowanym p&oacute;&#378;niej opiniom, nie&#378;le spisa&#322;o si&#281; niewielkie polskie lotnictwo oraz kawaleria. &bdquo;Mobilna i nie&#378;le wyposa&#380;ona w dzia&#322;ka przeciwpancerne by&#322;a w istocie najbardziej skutecznym &#347;rodkiem hamowania post&#281;p&oacute;w niemieckich czo&#322;g&oacute;w. Jednostki kawalerii potrafi&#322;y w p&oacute;&#378;niejszych etapach kampanii skuteczniej i d&#322;u&#380;ej wymyka&#263; si&#281; przeciwnikowi ni&#380; o wiele bardziej znu&#380;ona fizycznie odwrotowymi marszami piechota&rdquo;.<br>Po pobiciu Polski, Hitler zwr&oacute;ci&#322; si&#281; do Francji i Wielkiej Brytanii z ofert&#261; pokoju. Alianci jednak j&#261; odrzucili. Wojna dopiero si&#281; zaczyna&#322;a, a polscy &#380;o&#322;nierze mieli podczas niej zaznaczy&#263; swoj&#261; obecno&#347;&#263; na wszystkich niemal frontach.<br><br></span><span
                    class="c2 c5">Podczas pisania korzysta&#322;em m.in. z ksi&#261;&#380;ek Paw&#322;a Wieczorkiewicza, &bdquo;Ostatnie lata Polski niepodleg&#322;ej. Kampania 1939 roku&rdquo;, wydawnictwo LTW, 2013; Henryka Samsonowicza, Janusza Tazbira, Tadeusza &#321;epkowskiego, Tomasza Na&#322;&#281;cza, &bdquo;Polska. Losy pa&#324;stwa i narodu do 1939 roku&rdquo;, Wydawnictwo Iskry, Warszawa 2003; Czes&#322;awa Grzelaka, Henryka Sta&#324;czyka, &bdquo;Kampania polska 1939 roku. Pocz&#261;tek II wojny &#347;wiatowej&rdquo;, Warszawa 2006 i Tadeusza Olejnika, &bdquo;Wielu&#324; &ndash; polska Guernica, Wielu&#324;&rdquo; &ndash; Kielce 2009.</span>
          </p>
          <p class="c4"><span class="c8 c2"></span></p>
        </blockquote>
      </div>
    </div>
