<template>
  <div class="container ">
    <div class="row">
      <div class="col ps-4 pe-1 py-2 text-start">
        <blockquote>
          <p class="c0"><span class="c1">2 Korpus Polski wchodzi do gry</span></p>
          <p class="c0 c8"><span class="c4"></span></p>
          <p class="c0"><span class="c4">Anna D&#261;browska </span></p>
          <p class="c2"><span class="c7">Uwolnieni z sowieckich wi&#281;zie&#324; i &#322;agr&oacute;w Polacy wst&#281;powali w szeregi polskiej armii, kt&oacute;ra sta&#322;a si&#281; zacz&#261;tkiem najwi&#281;kszej jednostki Polskich Si&#322; Zbrojnych na Zachodzie. &#379;o&#322;nierze pod dow&oacute;dztwem gen. W&#322;adys&#322;awa Andersa ws&#322;awili si&#281;, zdobywaj&#261;c m.in Monte Cassino, Ankon&#281; i Boloni&#281;.</span><span
                  class="c3">&nbsp;</span></p>
          <p class="c2"><span class="c3">&bdquo;Poci&#261;g zatrzyma&#322; si&#281; na stacji Buzu&#322;uku, wyjrza&#322;em przez okno i ku swojemu zdumieniu zobaczy&#322;em &#380;o&#322;nierza w polskim mundurze. Wyskoczy&#322;em z wagonu i od tego kaprala dowiedzia&#322;em si&#281;, &#380;e trwa nab&oacute;r do polskiego wojska w ZSRR&rdquo; &ndash; tak o swojej drodze do 2 Korpusu Polskiego opowiada podpu&#322;kownik Marian Tomaszewski, &#380;o&#322;nierz 6 Pu&#322;ku Pancernego &bdquo;Dzieci Lwowskich&rdquo;. Pod koniec 1939 roku aresztowali go Sowieci i zes&#322;ali do Kazachstanu. Pracowa&#322; w kopalni w&#281;gla w Karagandzie, sk&#261;d po napa&#347;ci Niemiec na ZSRR w 1941 roku uda&#322;o mu si&#281; uciec. Gdy zosta&#322; &#380;o&#322;nierzem gen. Andersa, mia&#322; 19 lat.</span>
          </p>
          <p class="c2"><span class="c7">Od Syberii po Egipt</span><span class="c3">&nbsp;</span></p>
          <p class="c2"><span class="c3">&bdquo;2 Korpus Polski powsta&#322; na mocy rozkazu naczelnego wodza gen. W&#322;adys&#322;awa Sikorskiego z 29 czerwca 1943 roku&rdquo; &ndash; przypomina dr Janusz Kowalewski, historyk zajmuj&#261;cy si&#281; dziejami Polskich Si&#322; Zbrojnych na Zachodzie. Utworzenie jednostki poprzedzi&#322;o zawarcie 30 lipca 1941 roku porozumienia Sikorski-Majski, na mocy kt&oacute;rego rozpocz&#281;to w ZSRR formowanie Armii Polskiej na Wschodzie. W jej szeregi &#347;ci&#261;gali Polacy uwolnieni z &#322;agr&oacute;w i oboz&oacute;w sowieckich.</span>
          </p>
          <p class="c2"><span class="c3">Wobec narastaj&#261;cego konfliktu z w&#322;adzami sowieckimi, w 1942 roku &#380;o&#322;nierzy ewakuowano ze Zwi&#261;zku Radzieckiego. &bdquo;Nieludzk&#261; ziemi&#281;&rdquo; opu&#347;ci&#322;o w&oacute;wczas 116,5 tys. polskich obywateli, w tym ponad 78 tys. &#380;o&#322;nierzy. Powsta&#322;a w ZSRR Armia Polska sta&#322;a si&#281; g&#322;&oacute;wn&#261; si&#322;&#261; utworzonego 21 lipca 1943 roku w Iraku 2 Korpusu, najwi&#281;kszej jednostki Polskich Si&#322; Zbrojnych na Zachodzie. Na jej czele stan&#261;&#322; genera&#322; W&#322;adys&#322;aw Anders.</span>
          </p>
          <p class="c2"><span class="c3">W sk&#322;ad korpusu wesz&#322;y 5 i 6 Dywizja Piechoty, z kt&oacute;rych sformowano 5 Kresow&#261; Dywizj&#281; Piechoty. Do&#322;&#261;czono do nich Samodzieln&#261; Brygad&#281; Strzelc&oacute;w Karpackich, powo&#322;an&#261; w 1940 roku na Bliskim Wschodzie. Sta&#322;a si&#281; ona podstaw&#261; 3 Dywizji Strzelc&oacute;w Karpackich. 2 Korpus tworzy&#322;a te&#380; 2 Grupa Artylerii, 2 Warszawska Brygada Pancerna oraz mniejsze oddzia&#322;y. &bdquo;Razem z ojcem postanowili&#347;my zg&#322;osi&#263; si&#281; do armii w 1942 roku. Otrzyma&#322;em wtedy przydzia&#322; do 6 Batalionu&nbsp;&laquo;Dzieci Lwowskich&raquo;, przemianowanego potem na 6 Pu&#322;k Pancerny &laquo;Dzieci Lwowskich&raquo; 2 Warszawskiej Brygady Pancernej &ndash; opowiada major Otton&nbsp;Hulacki. W szeregach 2 Korpusu jako podchor&#261;&#380;y bra&#322; udzia&#322; w najwa&#380;niejszych bitwach pod wodz&#261; gen. Andersa.</span>
          </p>
          <p class="c2"><span class="c3">W sumie jesieni&#261; 1943 roku si&#322;y korpusu liczy&#322;y prawie 53 tys. &#380;o&#322;nierzy, w tym ponad 3 tys. oficer&oacute;w. &bdquo;Jednostka zosta&#322;a podporz&#261;dkowana brytyjskiej 9 Armii, kt&oacute;rej dow&oacute;dztwo wyznaczy&#322;o termin osi&#261;gni&#281;cia przez korpus gotowo&#347;ci na 1 stycznia 1944 roku&rdquo; &ndash; m&oacute;wi historyk. Na pocz&#261;tku polskie oddzia&#322;y ochrania&#322;y pola naftowe w p&oacute;&#322;nocnym Iraku. Potem &#380;o&#322;nierzy przeniesiono do Palestyny, gdzie przeszli szkolenie w g&oacute;rach, a nast&#281;pnie do Egiptu, gdzie otrzymali nowe uzbrojenie i wyposa&#380;enie.</span>
          </p>
          <p class="c2"><span class="c7">Na w&#322;oskiej ziemi</span><span class="c3">&nbsp;</span></p>
          <p class="c2"><span class="c3">W grudniu 1943 roku rozpocz&#261;&#322; si&#281; transport wojsk i sprz&#281;tu 2 Korpusu na po&#322;udnie W&#322;och. Jednym z pierwszych zada&#324; Polak&oacute;w na P&oacute;&#322;wyspie Apeni&#324;skim by&#322;o zdobycie wzg&oacute;rza i ufortyfikowanego klasztoru Monte Cassino, kt&oacute;re zamyka&#322;y drog&#281; do Rzymu. &bdquo;&#379;o&#322;nierze! D&#322;ugo czekali&#347;my na t&#281; chwil&#281; odwetu i zemsty nad odwiecznym naszym wrogiem. Zadanie, kt&oacute;re nam przypad&#322;o rozs&#322;awi na ca&#322;y &#347;wiat imi&#281; &#380;o&#322;nierza polskiego&rdquo;, napisa&#322; w rozkazie genera&#322; Anders. &bdquo;Nale&#380;a&#322;em do batalionu szturmowego, chcieli&#347;my z kolegami dorwa&#263; Niemc&oacute;w, wi&#281;c kiedy ruszy&#322; szturm, zapanowa&#322;a euforia&rdquo; &ndash; wspomina porucznik W&#322;adys&#322;aw D&#261;browski z 15 Pu&#322;ku U&#322;an&oacute;w Pozna&#324;skich. Po sze&#347;ciu dniach walki, 18 maja 1944 roku polskie oddzia&#322;y zdoby&#322;y klasztor, prze&#322;amuj&#261;c niemieckie umocnienia tzw. linii Gustawa. &bdquo;To by&#322;y dni, kt&oacute;rych nie zapomn&#281; do ko&#324;ca &#380;ycia&rdquo; przyznaje porucznik D&#261;browski.</span>
          </p>
          <p class="c2"><span class="c3">Kolejnym polskim sukcesem w kampanii w&#322;oskiej by&#322;o zdobycie 18 lipca Ankony na wybrze&#380;u Morza Adriatyckiego. &bdquo;Opanowanie tego portu zapewni&#322;o dostawy wojskom alianckim i u&#322;atwi&#322;o prowadzenie dalszych walk z Niemcami&rdquo; &ndash; wyja&#347;nia dr Kowalewski.</span>
          </p>
          <p class="c2"><span class="c3">Natomiast wiosn&#261; 1945 roku 2 Korpus bra&#322; udzia&#322; w bitwie o Boloni&#281;, tocz&#261;c walki z trzema doborowymi dywizjami niemieckimi. Mimo to 21 kwietnia Polacy jako pierwsi wkroczyli do miasta. Dow&oacute;dca 8 Armii brytyjskiej genera&#322; Mac Creery napisa&#322; do &#380;o&#322;nierzy: &bdquo;W tej wielkiej bitwie wykazali&#347;cie wspania&#322;ego ducha walki, wytrwa&#322;o&#347;&#263; i sprawno&#347;&#263; bojow&#261;&rdquo;.</span>
          </p>
          <p class="c2"><span class="c7">Wolna Polska</span><span class="c3">&nbsp;</span></p>
          <p class="c2"><span class="c3">Walka o Boloni&#281; by&#322;a ostatni&#261; batali&#261; 2 Korpusu, kt&oacute;ry po zako&#324;czeniu wojny pozosta&#322; we W&#322;oszech w sk&#322;adzie wojsk okupacyjnych. Jednocze&#347;nie w jego szeregi wst&#281;powali Polacy zwalniani z oboz&oacute;w i przymusowych rob&oacute;t. Latem 1945 roku korpus liczy&#322; ponad 100 tys. &#380;o&#322;nierzy.</span>
          </p>
          <p class="c2"><span class="c3">Po wojnie cz&#281;&#347;&#263; z nich, przewa&#380;nie szeregowych, zdecydowa&#322;a si&#281; na powr&oacute;t do kraju. W grudniu 1945 roku do Polski przyjecha&#322;o ponad 12 tys. &#380;o&#322;nierzy, potem wracali kolejni. W&#347;r&oacute;d nich por. D&#261;browski, kt&oacute;ry dowiedzia&#322; si&#281;, &#380;e jego matka i brat prze&#380;yli zsy&#322;k&#281; na Wsch&oacute;d i dotarli do Polski.</span>
          </p>
          <p class="c2"><span class="c3">&bdquo;Wielu z powracaj&#261;cych by&#322;o represjonowanych i prze&#347;ladowanych przez komunistyczne w&#322;adze&rdquo; &ndash; m&oacute;wi historyk. Wi&#281;kszo&#347;&#263; &#380;o&#322;nierzy, m.in. podpu&#322;kownik Tomaszewski i major Hulacki, zosta&#322;a jednak na Zachodzie i latem 1946 roku przetransportowano ich do Wielkiej Brytanii. W 1947 roku 2 Korpus Polski ostatecznie zosta&#322; rozwi&#261;zany.</span>
          </p>
          <p class="c2"><span class="c3">Ponad 4 tys. wojskowych, kt&oacute;rzy polegli podczas walk na P&oacute;&#322;wyspie Apeni&#324;skim, spoczywa na czterech polskich cmentarzach we W&#322;oszech: na Monte Cassino, w Bolonii, Loreto oraz na po&#322;udniu kraju w Casamassima. W&#347;r&oacute;d nich, na Monte Cassino, zgodnie ze swoj&#261; wol&#261; zosta&#322; pochowany gen. Anders, kt&oacute;ry po wojnie zamieszka&#322; w Londynie i tam zmar&#322; w 1970 roku.</span>
          </p>
          <p class="c2"><span class="c3">&bdquo;2 Korpus by&#322; dla jego &#380;o&#322;nierzy drug&#261; rodzin&#261;, szans&#261; na wyj&#347;cie z nieludzkiej sowieckiej ziemi, a jednocze&#347;nie dawa&#322; mo&#380;liwo&#347;&#263; walki za woln&#261; Polsk&#281;, kt&oacute;rej niestety wielu z nich nie doczeka&#322;o&rdquo; &ndash; m&oacute;wi senator Anna Anders, c&oacute;rka genera&#322;a.</span>
          </p>
          <p class="c6" id="h.gjdgxs"><span class="c3"></span></p>
        </blockquote>
      </div>
    </div>
  </div>
