<template>
  <div class="container ">
    <div class="row">
      <div class="col ps-4 pe-1 py-2">
        <blockquote>
          <p class="c5"><span class="c3 c6">&bdquo;Rosyjski walec&rdquo; na polskich Kresach </span></p>
          <p class="c4"><span class="c3 c6">Piotr Korczy&#324;ski </span></p>
          <p class="c4"><span class="c6">&nbsp;&bdquo;Cios w plecy!&rdquo; &ndash; tak brytyjski &bdquo;Times&rdquo; dzie&#324; po sowieckiej agresji na Polsk&#281; zatytu&#322;owa&#322; sw&#261; pierwsz&#261; szpalt&#281;. Tytu&#322; trafnie oddawa&#322; dzia&#322;ania Armii Czerwonej, kt&oacute;ra wype&#322;nia&#322;a sojusznicze zobowi&#261;zania wobec Wehrmachtu, ale jednocze&#347;nie wykorzystywa&#322;a bezczynno&#347;&#263; zachodnich sojusznik&oacute;w Polski, kt&oacute;rzy oddali j&#261; na pastw&#281; dw&oacute;ch totalitarnych s&#261;siad&oacute;w.</span><span
                  class="c3 c0">&nbsp;</span></p>
          <p class="c4"><span class="c0">Sobota 16 wrze&#347;nia 1939 roku by&#322;a czarnym dniem dla oddzia&#322;&oacute;w SS walcz&#261;cych w Polsce. Gwardia Hitlera zosta&#322;a mocno przetrzebiona przez polskich &#380;o&#322;nierzy. Najpierw w lasach jaworowskich oddzia&#322;y gen. Kazimierza Sosnkowskiego id&#261;ce na odsiecz Lwowa rozgromi&#322;y </span><span
                  class="c0"><a class="c1"
                                href="https://www.google.com/url?q=http://www.polska-zbrojna.pl/home/articleshow/25339?t%3DPolska-noc-SS-Germanii-http://polska-zbrojna.pl/home/articleshow/25339?t%3DPolska-noc-SS-Germanii-&amp;sa=D&amp;source=editors&amp;ust=1616449073408000&amp;usg=AOvVaw0aYs8tFvz33JInvqVq15bi">Pu&#322;k Zmotoryzowany SS &bdquo;Germania&rdquo;</a></span><span
                  class="c0">, a nast&#281;pnie pod Sochaczewem jedna bateria 17 Pu&#322;ku Artylerii Lekkiej zatrzyma&#322;a natarcie ca&#322;ej 4 Dywizji Pancernej, w tym elitarnego Pu&#322;ku &bdquo;Leibstandarte SS Adolf Hitler&rdquo;. Kilka polskich dzia&#322; uszkodzi&#322;o i zniszczy&#322;o 22 niemieckie czo&#322;gi i samochody pancerne! Te wiadomo&#347;ci wzbudzi&#322;y w kwaterze g&#322;&oacute;wnej F&uuml;hrera w&#347;ciek&#322;o&#347;&#263;. Hitler mia&#322; prawo by&#263; zdenerwowany &ndash; mimo zapewnie&#324; swych dow&oacute;dc&oacute;w, &#380;e w kilka dni zdob&#281;d&#261; Warszaw&#281;, stolica Polski wci&#261;&#380; si&#281; broni&#322;a, a Wojsko Polskie nadal by&#322;o zdolne zadawa&#263; bolesne ciosy. Na domiar z&#322;ego, Stalin zwleka&#322; z wype&#322;nieniem umowy zawartej </span><span
                  class="c0"><a class="c1"
                                href="https://www.google.com/url?q=http://www.polska-zbrojna.pl/home/articleshow/23417?t%3DPakt-Ribbentrop-Molotow-gra-i-zaglada&amp;sa=D&amp;source=editors&amp;ust=1616449073408000&amp;usg=AOvVaw2fTiWpDdE0EKPBMfpa7PqA">w pakcie Ribbentrop-Mo&#322;otow</a></span><span
                  class="c3 c0">&nbsp;i nie atakowa&#322; od wschodu Rzeczypospolitej, mimo coraz natarczywszych nawo&#322;ywa&#324; z Berlina.</span>
          </p>
          <p class="c4"><span class="c6">Kremlowskie kuranty</span><span class="c3 c0">&nbsp;</span></p>
          <p class="c4"><span class="c3 c0">Stalin op&oacute;&#378;nia&#322; atak na Polsk&#281;, gdy&#380; mimo sojuszu z Hitlerem, wci&#261;&#380; ogl&#261;da&#322; si&#281; na Zach&oacute;d. Wiedzia&#322;, &#380;e Armia Czerwona po czystce jej dow&oacute;dztwa, kt&oacute;r&#261; sam nakaza&#322;, nie jest jeszcze gotowa do walki z wojskami alianckimi. Jednak&#380;e tego samego dnia, w kt&oacute;rym polscy &#380;o&#322;nierze gromili pu&#322;ki SS, francuscy i angielscy sztabowcy na konferencji w Abbeville zdecydowali, &#380;e nie rusz&#261; zbrojnie Polakom na pomoc, o czym sowieccy agenci skwapliwie donie&#347;li w&#322;adzom na Kremlu. Po otrzymaniu tej wiadomo&#347;ci Stalin nie mia&#322; ju&#380; &#380;adnych opor&oacute;w i rozkaza&#322; s&#322;a&#263; wici do wojsk, kt&oacute;re skoncentrowa&#322; na swej zachodniej granicy.</span>
          </p>
          <p class="c4"><span class="c3 c0">Nad ranem 17 wrze&#347;nia Sowieci wezwali ambasadora Rzeczypospolitej w Moskwie Wac&#322;awa Grzybowskiego i wr&#281;czyli mu not&#281;, w kt&oacute;rej stwierdzono, &#380;e &bdquo;pa&#324;stwo polskie i jego Rz&#261;d przesta&#322;y faktycznie istnie&#263;. Dlatego te&#380; straci&#322;y wa&#380;no&#347;&#263; traktaty zawarte pomi&#281;dzy ZSRR a Polsk&#261;. [&hellip;] Rz&#261;d Radziecki nie mo&#380;e r&oacute;wnie&#380; pozosta&#263; oboj&#281;tny na fakt, &#380;e zamieszkuj&#261;ca terytorium Polski pokrewna ludno&#347;&#263; ukrai&#324;skiego i bia&#322;oruskiego pochodzenia jest bezbronna i zosta&#322;a pozostawiona w&#322;asnemu losowi. Rz&#261;d Radziecki poleci&#322; wobec powy&#380;szych okoliczno&#347;ci Naczelnemu Dow&oacute;dztwu Armii Czerwonej, aby nakaza&#322;o wojskom przekroczy&#263; granic&#281; i wzi&#261;&#263; pod sw&#261; opiek&#281; &#380;ycie i mienie ludno&#347;ci Zachodniej Ukrainy i Zachodniej Bia&#322;orusi&rdquo;. Stwierdzenie, &#380;e pa&#324;stwo polskie nie istnieje mia&#322;o zabezpieczy&#263; Zwi&#261;zek Sowiecki na wypadek reakcji mocarstw zachodnich i stworzy&#263; pozory neutralno&#347;ci w konflikcie mi&#281;dzy Niemcami a Francj&#261; i Wielk&#261; Brytani&#261;, co w pe&#322;ni si&#281; uda&#322;o.</span>
          </p>
          <p class="c4"><span class="c0">Wi&#281;cej nawet, gdy&#380; mimo &#380;e ambasador Grzybowski nie przyj&#261;&#322; k&#322;amliwej noty, polskie w&#322;adze, pod naciskiem zachodnich sojusznik&oacute;w, zaniecha&#322;y wydania o&#347;wiadczenia, &#380;e s&#261; w stanie wojny ze Zwi&#261;zkiem Sowieckim. Zach&oacute;d wci&#261;&#380; liczy&#322;, &#380;e przeci&#261;gnie Sowiet&oacute;w na swoj&#261; stron&#281;. Natomiast polskie w&#322;adze cywilne i wojskowe by&#322;y zaskoczone sowieckim atakiem, cho&#263; wywiad od dawna informowa&#322; o koncentracji wojsk sowieckich na granicy. Naczelny W&oacute;dz, marsza&#322;ek Edward Rydz-&#346;mig&#322;y dopiero wieczorem 17 wrze&#347;nia wyda&#322; dyrektyw&#281;, by Warszawa i Modlin stawia&#322;y op&oacute;r a&#380; do wyczerpania amunicji, a wszystkie </span><span
                  class="c0"><a class="c1"
                                href="https://www.google.com/url?q=http://www.polska-zbrojna.pl/home/articleshow/29098?t%3DDo-broni-&amp;sa=D&amp;source=editors&amp;ust=1616449073409000&amp;usg=AOvVaw0u1r6oNLvksXq081X66BNn">oddzia&#322;y wycofywa&#322;y si&#281; na W&#281;gry lub do Rumunii</a></span><span
                  class="c3 c0">. Na walk&#281; z Sowietami zezwala&#322; tylko w&oacute;wczas, gdyby atakowali oni polskie wojska lub pr&oacute;bowali je rozbraja&#263;. To ostatnie stwierdzenie wprowadzi&#322;o jedynie chaos, gdy&#380; polscy dow&oacute;dcy r&oacute;&#380;nie je interpretowali i nie byli pewni, jak maj&#261; si&#281; zachowa&#263;. Tym bardziej &#380;e cz&#281;sto Sowieci w celach dywersyjnych g&#322;osili, &#380;e id&#261; Polsce z pomoc&#261;.</span>
          </p>
          <p class="c4"><span class="c0">W nocy z 17 na 18 wrze&#347;nia prezydent oraz rz&#261;d Rzeczypospolitej udali si&#281; do Rumunii. </span><span
                  class="c0"><a class="c1"
                                href="https://www.google.com/url?q=http://www.polska-zbrojna.pl/home/articleshow/29594?t%3DRozliczenia-po-Wrzesniu-39-Dramaty-i-polityczne-gry&amp;sa=D&amp;source=editors&amp;ust=1616449073410000&amp;usg=AOvVaw1Tt3pBy9T1t0WbPs8lUdbw">Podobnie uczyni&#322;, wraz ze swym sztabem, marsza&#322;ek Rydz-&#346;mig&#322;y</a></span><span
                  class="c3 c0">, co w znacznej cz&#281;&#347;ci polskiego spo&#322;ecze&#324;stwa wywo&#322;a&#322;o oburzenie i spowodowa&#322;o oskar&#380;enia o porzucenie walcz&#261;cego wci&#261;&#380; wojska.</span>
          </p>
          <p class="c4"><span class="c6">Boje z sowieckim kolosem</span><span class="c0 c3">&nbsp;</span></p>
          <p class="c4"><span class="c3 c0">Nacieraj&#261;ce na Polsk&#281; wojska sowieckie zosta&#322;y uszykowane w dwa fronty. Front Bia&#322;oruski komandarma Michai&#322;a Kowalowa w pierwszych godzinach ataku liczy&#322; 15 dywizji piechoty i kawalerii oraz siedem brygad pancernych (ponad 2000 czo&#322;g&oacute;w i samochod&oacute;w pancernych) &ndash; w sumie ponad 200 tys. &#380;o&#322;nierzy i liczba ta w kolejnych dniach jeszcze si&#281; zwi&#281;kszy&#322;a. Natomiast Front Ukrai&#324;ski komandarma Siemiona Timoszenki mia&#322; w pierwszym rzucie 16 dywizji piechoty i kawalerii oraz osiem brygad pancernych (blisko 3000 czo&#322;g&oacute;w i samochod&oacute;w pancernych), czyli ponad 265 tys. &#380;o&#322;nierzy, wspieranych przez flot&#281; powietrzn&#261; licz&#261;c&#261; oko&#322;o tysi&#261;ca samolot&oacute;w. Ponadto regularne oddzia&#322;y Armii Czerwonej wspar&#322;y grupy dywersyjne, tworzone i uzbrajane przez sowiecki wywiad wojskowy i NKWD z bia&#322;oruskich, ukrai&#324;skich i &#380;ydowskich obywateli Rzeczypospolitej.</span>
          </p>
          <p class="c4"><span class="c3 c0">Tej nawale na Kresach Wschodnich Polacy mogli przeciwstawi&#263; niewsp&oacute;&#322;miernie s&#322;absze si&#322;y. Dow&oacute;dztwo polskie pozostawi&#322;o do ochrony tych ziem oko&#322;o 400 tys. &#380;o&#322;nierzy. Granicy bezpo&#347;rednio strzeg&#322;o blisko 12 tys. &#380;o&#322;nierzy Korpusu Ochrony Pogranicza, zorganizowanych w 25 batalionach i siedmiu szwadronach kawalerii. Wi&#281;kszo&#347;&#263; tych oddzia&#322;&oacute;w, mimo przyt&#322;aczaj&#261;cej przewagi nieprzyjaciela, podj&#281;&#322;a z nim walk&#281;. Zaci&#281;te boje trwa&#322;y na Wile&#324;szczy&#378;nie, Nowogr&oacute;dczy&#378;nie i Podolu. Przez trzy dni sowieck&#261; nawa&#322;&#281; powstrzymywa&#322; batalion forteczny KOP &bdquo;Sarny&rdquo; na umocnionej pozycji pod S&#322;uczem. 20 i 21 wrze&#347;nia dosz&#322;o do walk ulicznych w Grodnie, w czasie kt&oacute;rych znaczne straty ponios&#322;y sowieckie oddzia&#322;y pancerne. 22 wrze&#347;nia pod Kodziowcami 101 Pu&#322;k U&#322;an&oacute;w z powodzeniem zatrzyma&#322; natarcie Sowiet&oacute;w, niszcz&#261;c im wiele czo&#322;g&oacute;w.</span>
          </p>
          <p class="c4"><span class="c0">Najwi&#281;cej star&#263; z Armi&#261; Czerwon&#261; mia&#322;a na koncie wycofuj&#261;ca si&#281; od granicy na Polesie grupa KOP gen. Wilhelma Orlika-R&uuml;ckemana. Jej najwi&#281;kszym sukcesem by&#322;o rozgromienie 28 wrze&#347;nia pod Szackiem sztabu i cz&#281;&#347;ci oddzia&#322;&oacute;w 52 Dywizji Strzeleckiej. R&oacute;wnie skutecznie walczy&#322; w rejonie Borowicz, Nawozu i Hruziatyna 3 Pu&#322;k Piechoty KOP p&#322;k. Zdzis&#322;awa Zaj&#261;czkowskiego. Tak&#380;e oddzia&#322;y Samodzielnej Grupy Operacyjnej &bdquo;Polesie&rdquo; </span><span
                  class="c0"><a class="c1"
                                href="https://www.google.com/url?q=http://www.polska-zbrojna.pl/home/articleshow/24659?t%3DOstatni-general-wrzesnia-1939&amp;sa=D&amp;source=editors&amp;ust=1616449073411000&amp;usg=AOvVaw37c-FhdZJuJfCjjgXp0uip">gen. Franciszka Kleeberga</a></span><span
                  class="c3 c0">&nbsp;&#347;ciera&#322;y si&#281; niejednokrotnie z Armi&#261; Czerwon&#261;, m.in. pod Jab&#322;oni&#261; i Milanowem.</span>
          </p>
          <p class="c4"><span class="c3 c0">Jednak polskie wojska nie mia&#322;y szans, by zatrzyma&#263; sowieckiego kolosa. Mimo &#380;e kampania polska wyra&#378;nie pokaza&#322;a, &#380;e Armi&#261; Czerwon&#261; dowodz&#261; przewa&#380;nie niekompetentni dow&oacute;dcy, a &#380;o&#322;nierze s&#261; s&#322;abo wyszkoleni i wyposa&#380;eni, to jednak sam&#261; sw&#261; mas&#261; &ndash; os&#322;awionym &bdquo;rosyjskim walcem&rdquo; &ndash; dos&#322;ownie zalali polskie Kresy. Sowieci r&oacute;wnie&#380; bardzo szybko przestali udawa&#263;, &#380;e nie wsp&oacute;&#322;pracuj&#261; z Niemcami, czego wymownym symbolem by&#322;a m.in. wsp&oacute;lna defilada wojskowa w Brze&#347;ciu nad Bugiem. &bdquo;Prawda&rdquo; od 23 wrze&#347;nia zacz&#281;&#322;a publikowa&#263; mapki z lini&#261; rozgraniczaj&#261;c&#261; niemieckie i sowieckie zdobycze, dowodz&#261;c dobitnie, &#380;e III Rzesza i Zwi&#261;zek Sowiecki dokonuj&#261; w&#322;a&#347;nie czwartego rozbioru Polski.</span>
          </p>
          <p class="c4" id="h.gjdgxs"><span class="c6">Bibliografia</span><span class="c3 c0">&nbsp;</span></p>
          <p class="c4"><span class="c0 c7">Cz. Brzoza, &bdquo;Polska w czasach niepodleg&#322;o&#347;ci i drugiej wojny &#347;wiatowej (1918&ndash;1945)&rdquo;, [w:] &bdquo;Wielka Historia Polski&rdquo; tom 9, Krak&oacute;w 2001</span><span
                  class="c0">&nbsp;<br></span><span class="c0 c7">Cz. Grzelak, &bdquo;Kresy w czerwieni. Agresja Zwi&#261;zku Sowieckiego na Polsk&#281; w 1939 roku&rdquo;, Warszawa 1998</span><span
                  class="c0">&nbsp;<br></span><span class="c0 c7">S. D&#281;bski, &bdquo;Mi&#281;dzy Berlinem a Moskw&#261;. Stosunki niemiecko-sowieckie 1939&ndash;1941&rdquo;, Warszawa 2007</span><span
                  class="c3 c0">&nbsp;</span></p>
          <p class="c8"><span class="c3 c0"></span></p>
        </blockquote>
      </div>
    </div>
  </div>
