<template>
  <div class="container ">
    <div class="row">
      <div class="col ps-4 pe-1 py-2 text-start">
        <blockquote>
          <p class="c3"><span class="c4">Zwyci&#281;ski w&oacute;dz</span></p>
          <p class="c0"><span class="c4">Anna D&#261;browska</span></p>
          <p class="c0"><span class="c5">Gen. W&#322;adys&#322;aw Anders, dow&oacute;dca Armii Polskiej w ZSRR oraz 2 Korpusu Polskiego, zas&#322;yn&#261;&#322; jako dow&oacute;dca, kt&oacute;rego &#380;o&#322;nierze zdobyli w maju 1944 roku w&#322;oskie wzg&oacute;rze Monte Cassino. Genera&#322; cieszy&#322; si&#281; wielkim autorytetem w&#347;r&oacute;d swoich podkomendnych, by&#322; dla nich nie tylko dow&oacute;dc&#261;. Traktowali go jak ojca, kt&oacute;remu ufali.</span>
          </p>
          <p class="c0"><span class="c1">&bdquo;Gen. W&#322;adys&#322;aw Anders by&#322; zdolnym &#380;o&#322;nierzem, &#347;wietnym kawalerzyst&#261; i uzdolnionym dow&oacute;dc&#261;, kt&oacute;rego kochali podkomendni, a tak&#380;e politykiem, kt&oacute;ry nie mia&#322; z&#322;udze&#324; co do powojennej komunistycznej w&#322;adzy w Polsce&rdquo; &ndash; m&oacute;wi dr Piotr Jagodzi&#324;ski, historyk zajmuj&#261;cy si&#281; dziejami Polskich Si&#322; Zbrojnych na Zachodzie.</span>
          </p>
          <p class="c0"><span class="c1">W&#322;adys&#322;aw Anders urodzi&#322; si&#281; 11 sierpnia 1892 roku w B&#322;oniu ko&#322;o Kutna, w rodzinie ziemia&#324;skiej. Po maturze trafi&#322; do armii rosyjskiej. Uko&#324;czy&#322; szko&#322;&#281; podoficersk&#261; w stopniu chor&#261;&#380;ego rezerwy i rozpocz&#261;&#322; studia politechniczne w Rydze. W czasie I wojny &#347;wiatowej zosta&#322; powo&#322;any do carskiej armii, w kt&oacute;rej dowodzi&#322; szwadronem jazdy. Potem uko&#324;czy&#322; kurs Akademii Sztabu Generalnego w Petersburgu i zosta&#322; wys&#322;any na front do Rumunii jako szef sztabu jednej z dywizji piechoty.</span>
          </p>
          <p class="c0"><span class="c5">Dla niepodleg&#322;ej</span></p>
          <p class="c0"><span class="c1">&bdquo;Ca&#322;y czas Anders my&#347;la&#322; o walce dla Polski, dlatego po rewolucji lutowej w lipcu 1917 roku wst&#261;pi&#322; do tworzonego na terenie Rosji I Korpusu Polskiego dowodzonego przez gen. J&oacute;zefa Dowbora-Mu&#347;nickiego&rdquo; &ndash; m&oacute;wi historyk. Po rozbrojeniu Korpusu przez Niemc&oacute;w w maju 1918 roku przedosta&#322; si&#281; do kraju i zg&#322;osi&#322; do polskiego wojska.</span>
          </p>
          <p class="c0"><span class="c1">Bra&#322; udzia&#322; w powstaniu wielkopolskim jako szef sztabu Armii Wielkopolskiej, a podczas wojny polsko-bolszewickiej dowodzi&#322; 15 Pu&#322;kiem U&#322;an&oacute;w Pozna&#324;skich. Da&#322; dowody swych talent&oacute;w wojskowych, m.in. w starciach nad Berezyn&#261;. Za m&#281;stwo w walce z bolszewikami zosta&#322; odznaczony przez marsza&#322;ka J&oacute;zefa Pi&#322;sudskiego orderem Virtuti Militari V klasy.</span>
          </p>
          <p class="c0"><span class="c1">Po wojnie m&#322;ody oficer studiowa&#322; w s&#322;ynnej paryskiej Wy&#380;szej Szkole Wojennej, a po powrocie kierowa&#322; kursem dla wy&#380;szych dow&oacute;dc&oacute;w w Wy&#380;szej Szkole Wojennej w Warszawie i by&#322; komendantem garnizonu w Warszawie. W trakcie zamachu majowego p&#322;k Anders opowiedzia&#322; si&#281; po stronie rz&#261;du i zorganizowa&#322; ewakuacj&#281; prezydenta Stanis&#322;awa Wojciechowskiego z Belwederu. &ndash; Stani&#281;cie po przeciwnej stronie ni&#380; marsza&#322;ek Pi&#322;sudski nie zahamowa&#322;o jednak jego dalszej wojskowej kariery &ndash; podkre&#347;la badacz.</span>
          </p>
          <p class="c0"><span class="c1">Szczeg&oacute;lne uznanie w&#322;adz wojskowych pu&#322;kownik zyska&#322; po wielkich manewrach kawaleryjskich na Wo&#322;yniu. &bdquo;Anders jest oficerem, przed kt&oacute;rym w przysz&#322;o&#347;ci powinny sta&#263; otworem wszystkie, nawet najwy&#380;sze, stanowiska w wojsku&rdquo;, stwierdzi&#322; po &#263;wiczeniach gen. Gustaw Orlicz-Dreszer, dow&oacute;dca 2 Dywizji Kawalerii. Nied&#322;ugo potem p&#322;k Anders zosta&#322; szefem sztabu w Generalnym Inspektoracie Jazdy. Potem dowodzi&#322; Brygad&#261; Kawalerii w Brodach, a w 1937 roku, ju&#380; jako genera&#322; brygady, obj&#261;&#322; dow&oacute;dztwo Nowogr&oacute;dzkiej Brygady Kawalerii w Baranowiczach.</span>
          </p>
          <p class="c0"><span class="c1">&bdquo;Gen. Anders by&#322; nie tylko &#347;wietnym kawalerzyst&#261;, ale tak&#380;e je&#378;d&#378;cem&rdquo; &ndash; dodaje dr Jagodzi&#324;ski. W 1932 roku przewodzi&#322; polskiej ekipie, kt&oacute;ra podczas zawod&oacute;w hippicznych w Nicei zdoby&#322;a cztery pierwsze nagrody, w tym najwa&#380;niejsz&#261; &ndash; Puchar Narod&oacute;w.</span>
          </p>
          <p class="c0"><span class="c5">Z nieludzkiej ziemi</span></p>
          <p class="c0"><span class="c1">Po wybuchu II wojny genera&#322; na czele Nowogr&oacute;dzkiej Brygady Kawalerii, a potem Grupy Operacyjnej Kawalerii walczy&#322; z Niemcami, m.in. w okolicach P&#322;ocka, Mi&#324;ska Mazowieckiego i Tomaszowa Lubelskiego. Po napa&#347;ci na Polsk&#281; 17 wrze&#347;nia Armii Czerwonej, gen. Anders podzieli&#322; oddzia&#322;y na mniejsze grupy i poleci&#322; im przebija&#263; si&#281; na W&#281;gry. Sam 29 wrze&#347;nia zosta&#322; ranny i w okolicach Sambora ko&#322;o Lwowa dosta&#322; si&#281; do sowieckiej niewoli.</span>
          </p>
          <p class="c0"><span class="c1">By&#322; przetrzymywany m.in. w centralnym wi&#281;zieniu NKWD na &#321;ubiance w Moskwie. &bdquo;Sowieci wielokrotnie go przes&#322;uchiwali i bezskutecznie nak&#322;aniali do wst&#261;pienia do Armii Czerwonej, jednak genera&#322; zdecydowanie odrzuca&#322; te propozycj&#281;&rdquo; &ndash; zaznacza historyk.</span>
          </p>
          <p class="c0"><span class="c1">Sytuacj&#281; Andersa zmieni&#322; wybuch wojny sowiecko-niemieckiej w czerwcu 1941 roku i podpisanie 30 lipca uk&#322;adu Sikorski-Majski, kt&oacute;ry przewidywa&#322; uwolnienie Polak&oacute;w z sowieckich &#322;agr&oacute;w oraz utworzenie polskiego wojska na terenie Zwi&#261;zku Radzieckiego. Na pocz&#261;tku sierpnia genera&#322; zosta&#322; zwolniony z wi&#281;zienia i obj&#261;&#322; dow&oacute;dztwo nad powstaj&#261;c&#261; armi&#261; polsk&#261;.</span>
          </p>
          <p class="c0"><span class="c1">Do punkt&oacute;w werbunkowych zg&#322;asza&#322;y si&#281; tysi&#261;ce &#380;o&#322;nierzy i cywili. Genera&#322; stara&#322; si&#281; o zapewnienie im broni, umundurowania i &#380;ywno&#347;ci. Prowadzi&#322; r&oacute;wnie&#380; rozmowy z w&#322;adzami radzieckimi na temat zagini&#281;cia polskich oficer&oacute;w aresztowanych w 1939 roku przez Sowiet&oacute;w. Ich los pozna&#322; dopiero w kwietniu 1943 roku, po odkryciu przez Niemc&oacute;w w Katyniu grob&oacute;w Polak&oacute;w.</span>
          </p>
          <p class="c0"><span class="c1">W zwi&#261;zku z k&#322;opotami organizacyjnymi i zaopatrzeniowymi, genera&#322; naciska&#322; na ewakuacj&#281; Armii Polskiej do kontrolowanego przez Brytyjczyk&oacute;w Iranu. &bdquo;Dzi&#281;ki jego staraniom w marcu i sierpniu 1942 roku ze Zwi&#261;zku Sowieckiego zosta&#322;o ewakuowanych ponad 115 tys. polskich obywateli, w tym ok. 70 tys. &#380;o&#322;nierzy&rdquo; &ndash; wylicza dr Jagodzi&#324;ski.</span>
          </p>
          <p class="c0"><span class="c1">W Iranie oddzia&#322;y zosta&#322;y przemianowane na Armi&#281; Polsk&#261; na Wschodzie. Polacy odbyli przeszkolenie w Iraku i Palestynie, a w lipcu 1943 roku, po po&#322;&#261;czeniu z Samodzieln&#261; Brygad&#261; Strzelc&oacute;w Karpackich, powsta&#322; licz&#261;cy ok. 50 tys. &#380;o&#322;nierzy 2 Korpus Polski, na kt&oacute;rego czele stan&#261;&#322; gen. Anders. &bdquo;Genera&#322; cieszy&#322; si&#281; wielkim autorytetem w&#347;r&oacute;d swoich &#380;o&#322;nierzy, by&#322; dla nich nie tylko dow&oacute;dc&#261;, lecz r&oacute;wnie&#380; ojcem, kt&oacute;remu ufali&rdquo; &ndash; podkre&#347;la historyk.</span>
          </p>
          <p class="c0"><span class="c5">Walka o wzg&oacute;rze</span></p>
          <p class="c0"><span class="c1">Na prze&#322;omie 1943 i 1944 roku jednostki polskie zosta&#322;y przetransportowane do W&#322;och i podporz&#261;dkowane brytyjskiej 8 Armii. Podczas kampanii w&#322;oskiej 2 KP ws&#322;awi&#322; si&#281; przede wszystkim zdobyciem silnie ufortyfikowanego wzg&oacute;rza Monte Cassino zamykaj&#261;cego drog&#281; do Rzymu.</span>
          </p>
          <p class="c0"><span class="c7">Kiedy gen. Oliver Leese, dow&oacute;dca 8 Armii zaproponowa&#322; gen. Andersowi zdobywanie przez Polak&oacute;w wzg&oacute;rza, polski dow&oacute;dca zdawa&#322; sobie spraw&#281;, &#380;e b&#281;dzie to trudne zadanie, ale przyczyni si&#281; do wzmocnienia pozycji Polski w&#347;r&oacute;d aliant&oacute;w. &bdquo;Wykonanie tego zadania [&hellip;] by&#322;oby najlepsz&#261; odpowiedzi&#261; na propagand&#281; sowieck&#261;, kt&oacute;ra twierdzi&#322;a, &#380;e Polacy nie chc&#261; si&#281; bi&#263; z Niemcami. Podtrzyma&#322;oby na duchu op&oacute;r walcz&#261;cego Kraju. Przynios&#322;oby du&#380;&#261; chwa&#322;&#281; or&#281;&#380;owi polskiemu&rdquo;, zapisa&#322; gen. Anders w ksi&#261;&#380;ce </span><span
                  class="c6">Bez ostatniego rozdzia&#322;u</span><span class="c1">.</span></p>
          <p class="c0"><span class="c1">Po tygodniu ci&#281;&#380;kich walk 18 maja 1944 roku Polacy zdobyli Monte Cassino. Potem &#380;o&#322;nierze 2 Korpusu pod wodz&#261; Andersa zdobyli tak&#380;e Ankon&#281;, prze&#322;amali lini&#281; Got&oacute;w i zdobyli Boloni&#281;. Niestety, wk&#322;ad Polak&oacute;w w walk&#281; z Niemcami nie wp&#322;yn&#261;&#322; na decyzje aliant&oacute;w podj&#281;te w lutym 1945 roku na konferencji w Ja&#322;cie i pozostawienie Polski w sowieckiej strefie wp&#322;yw&oacute;w.</span>
          </p>
          <p class="c0"><span class="c1">&bdquo;Melduj&#281;, &#380;e II Korpus nie mo&#380;e uzna&#263; jednostronnej decyzji, oddaj&#261;cej Polsk&#281; i nar&oacute;d na &#322;up bolszewikom. Zwr&oacute;ci&#322;em si&#281; do w&#322;adz sojuszniczych o wycofanie korpusu z odcink&oacute;w bojowych, bo nie mam sumienia &#380;&#261;da&#263; w obecnej chwili od &#380;o&#322;nierza ofiary krwi&rdquo;, napisa&#322; gen. Anders w telegramie do prezydenta W&#322;adys&#322;awa Raczkiewicza dzie&#324; po og&#322;oszeniu postanowie&#324; wielkiej tr&oacute;jki. Alianci odm&oacute;wili jednak wycofania &#380;o&#322;nierzy polskich z frontu, t&#322;umacz&#261;c si&#281; wzgl&#281;dami operacyjnymi.</span>
          </p>
          <p class="c0"><span class="c1">Po zako&#324;czeniu wojny genera&#322; nadal troszczy&#322; si&#281; o swoich &#380;o&#322;nierzy. Organizowa&#322; dla nich warsztaty pracy, szko&#322;y, kursy zawodowe i wy&#380;sze studia na w&#322;oskich uczelniach, aby u&#322;atwi&#263; im start w cywilnym &#380;yciu. &ndash; Genera&#322; nie mia&#322; &#380;adnych z&#322;udze&#324; co do rzeczywistych zamiar&oacute;w w&#322;adzy ludowej i postanowi&#322; pozosta&#263; na emigracji w Londynie &ndash; t&#322;umaczy dr Jagodzi&#324;ski. Zreszt&#261; ju&#380; w 1946 roku w&#322;adze komunistyczne pozbawi&#322;y go obywatelstwa polskiego, m.in. za to, &#380;e nie podporz&#261;dkowa&#322; si&#281; dow&oacute;dztwu ludowego wojska polskiego. Dopiero w 1989 roku rz&#261;d PRL uchyli&#322; t&#281; uchwa&#322;&#281;.</span>
          </p>
          <p class="c0" id="h.gjdgxs"><span class="c1">Na emigracji gen. Anders anga&#380;owa&#322; si&#281; w dzia&#322;alno&#347;&#263; polityczn&#261;, by&#322; m.in. przewodnicz&#261;cym Skarbu Narodowego, kt&oacute;ry mia&#322; zasila&#263; dzia&#322;ania niepodleg&#322;o&#347;ciowe, a w latach 1946-1954 Naczelnym Wodzem i Generalnym Inspektorem Polskich Si&#322; Zbrojnych. &nbsp;&bdquo;Dla emigracji niepodleg&#322;o&#347;ciowej Anders by&#322; niekwestionowanym autorytetem. By&#322; bohaterem walk we W&#322;oszech i cz&#322;owiekiem, kt&oacute;ry uratowa&#322; tysi&#261;ce Polak&oacute;w z sowieckich &#322;agr&oacute;w, a tak&#380;e symbolem bezkompromisowego oporu wobec nowej w&#322;adzy w Polsce&rdquo; &ndash; m&oacute;wi historyk.</span>
          </p>
          <p class="c0"><span class="c1">Genera&#322; zmar&#322; 12 maja 1970 roku, w 26. rocznic&#281; rozpocz&#281;cia polskiej bitwy pod Monte Cassino. Zgodnie z jego wol&#261; zosta&#322; pochowany w&#347;r&oacute;d swoich &#380;o&#322;nierzy na Polskim Cmentarzu Wojennym na Monte Cassino. W 1995 roku W&#322;adys&#322;aw Anders zosta&#322; po&#347;miertnie odznaczony Orderem Or&#322;a Bia&#322;ego, najwy&#380;szym polskim odznaczeniem.</span>
          </p>
          <p class="c8"><span class="c9"></span></p>
        </blockquote>
      </div>
    </div>
  </div>
